.cartpage {
	.fullpage {
		padding: 0;
		@include media-breakpoint-down(md) {
			padding-top: 50px; } }
	header, footer {
		display: none; } }

@import "../includes/cartheader";
@import "../includes/cartfooter";

// cart page

.cart-table {
	.item {
		border-bottom: 1px dashed darken($white,5%);
		margin-bottom: 10px;
		padding-bottom: 10px;
		&:last-child {
 }			// border-bottom: none
		.cart-item {
			@include flexbox;
			flex-wrap: wrap;
			@include pos(rel,null);
			@include media-breakpoint-down(sm) {
				padding-bottom: 25px; }
			.btn-remove {
				display: block;
				@include pos(abs, null 0 0 null);
				color: darken($white,40%);
				.material-icons {
					@include fs(18rem);
					display: block; }
				&:hover {
					color: $red; } }
			.img {
				width: 100px;
				height: 100px;
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				@include media-breakpoint-down(sm) {
					width: 60px;
					height: 60px; }
				img {
					max-width: 90%;
					max-height: 90%; } }
			.caption {
				width: calc(100% - 100px);
				padding-left: 20px;
				@include flexbox;
				flex-wrap: wrap;
				justify-content: space-between;
				@include media-breakpoint-down(sm) {
					width: calc(100% - 60px); }
				.item-info {
					width: calc(100% - 150px);
					@include media-breakpoint-down(sm) {
						width: calc(100% - 100px); }
					.name {
						display: block;
						@include fs(14rem);
						font-weight: 500;
						@include media-breakpoint-down(sm) {
							@include fs(13rem); } }
					.option {
						@include fs(13rem);
						color: darken($white,40%);
						margin-top: 0px;
						@include media-breakpoint-down(sm) {
							@include fs(12rem); }
						span {
							margin-left: 5px;
							color: darken($white,60%); } }
					.amount {
						@include flexbox;
						align-items: center;
						align-content: center;
						@include fs(13rem);
						color: darken($white,40%);
						margin-top: 5px;
						@include media-breakpoint-down(sm) {
							@include fs(12rem); }
						.input-number {
							margin-left: 5px;
							color: $main; } } }
				.item-price {
					@include fs(15rem);
					text-align: right;
					width: 150px;
					@include media-breakpoint-down(sm) {
						@include fs(13rem);
						width: 100px; }
					.new {
						color: $bluegrey;
						font-weight: 700; }
					.old {
						@include fs(13rem);
						del {
							color: darken($white,40%); }
						span {
							color: $main;
							margin-left: 5px; } }
					.des {
						margin-top: 5px;
						@include fs(12rem);
						font-style: italic;
						color: darken($white,40%);
						@include media-breakpoint-down(sm) {
							@include fs(10rem); } } }
				.gift-list {
					margin-top: 10px;
					@include bor-rad(5px);
					border: 1px dashed color-rgba($blue,0.5);
					padding: 5px 15px;
					width: 100%;
					max-width: 300px;
					label {
						@include fs(14rem);
						line-height: 1;
						color: $blue;
						font-weight: 500;
						background: $white;
						margin-bottom: 10px;
						@include media-breakpoint-down(sm) {
							@include fs(13rem); }
						&:before {
							content: '\f06b';
							margin-right: 5px; } }
					ul {
						list-style-type: disc;
						padding-left: 20px;
						li {
							margin-bottom: 3px;
							&:last-child {
								margin-bottom: 0; }
							a {
								display: block;
								@include fs(0);
								span {
									@include fs(13rem);
									line-height: 1.2; }
								&:hover {
									color: $blue; } } } }
					.gift-item {
						@include flexbox;
						.gift-img {
							width: 40px;
							height: 40px;
							@include flexbox;
							align-items: center;
							align-content: center;
							justify-content: center;
							img {
								max-width: 90%;
								max-height: 90%; } }
						.gift-caption {
							padding-left: 10px;
							.gift-name {
								@include fs(13rem);
								font-weight: 500; }
							.gift-price {
								.new {
									@include fs(13rem);
									font-weight: 700;
									color: $bluegrey; } } } } } } } } }
.cart-step {
	@include pos(rel,null);
	background: $white;
	@include bor-rad(5px);
	overflow: hidden;
	box-shadow: 0px 5px 20px color-rgba($black,0.05);
	padding: 15px;
	margin-bottom: 20px;
	@include media-breakpoint-down(md) {
		margin: 0px -15px 10px;
		padding: 15px;
		@include bor-rad(0);
		box-shadow: none; }
	&.cart-step1 {
		@include media-breakpoint-down(md) {
			margin: 10px -15px 10px; } }
	.cart-title {
		@include flexbox;
		align-items: center;
		align-content: center;
		border-bottom: 1px solid darken($white,10%);
		padding-bottom: 10px;
		margin-bottom: 10px;
		.material-icons {
			padding-right: 10px;
			@include media-breakpoint-down(sm) {
				@include fs(18rem); } }
		span {
			@include fs(16rem);
			font-weight: 500;
			@include media-breakpoint-down(sm) {
				@include fs(14rem); }
			strong {
				color: $red;
				font-weight: 700; } } }
	.cart-total {
		// +flexbox
		// flex-direction: column
		// align-items: flex-end
		// align-content: flex-end
		.group {
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: space-between;
			// width: 100%
			// max-width: 250px
			.label {
				@include fs(13rem); }
			.value {
				@include fs(15rem);
				font-weight: 700;
				color: $bluegrey; }
			&.total {
				.value {
					@include fs(18rem);
					color: $red; } } } }
	.cart-btn {
		padding: 15px 0;
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: space-between;
		@include media-breakpoint-down(md) {
			@include pos(fix, null 0 0 0);
			z-index: 50;
			padding: 10px;
			background: $white;
			box-shadow: 0px 0px 20px color-rgba($black,0.05); }
		.total {
			@include fs(14rem);
			color: $main;
			strong {
				color: $red;
				font-weight: 700;
				@include fs(16rem);
				margin-left: 5px; }
			.des {
				display: block;
				@include fs(12rem);
				line-height: 1;
				color: darken($white,40%);
				font-style: italic; } } }
	.btn-back {
		@include fs(14rem);
		font-weight: 500;
		color: $blue;
		@include pos(abs, 15px 15px null null);
		&:hover {
			color: $blue; }
		// &:before
		// 	content: '\f104'
		// 	font-weight: bold
 }		// 	margin-right: 5px
	.btn-continue {
		@extend .btn-main;
		background: $orangegradient;
		text-transform: uppercase;
		padding: 0px 40px;
		@include media-breakpoint-down(sm) {
			@include fs(13rem);
			padding: 0px 20px; }
		&:hover {
			box-shadow: 0px 10px 20px -10px $orange; } }
	.cart-login {
		.form-wrap {
			margin-bottom: 10px;
			.form-select {
				margin-bottom: 10px;
				label {
					font-weight: normal; } }
			.form-group {
				label {
					display: none; } }
			input[type="submit"] {
				text-transform: uppercase;
				font-weight: 500;
				border: none; } }
		.social-login {} }
	.btn-getaddress {
		display: inline-block;
		padding: 0px 15px;
		@include bor-rad(3px);
		@include fs(14rem);
		line-height: 30px;
		color: $blue;
		font-weight: 500;
		margin-bottom: 10px;
		&:before {
			content: '+';
			font-weight: bold;
			margin-right: 5px; } }
	.cart-form {
		.form-wrap {
			.form-group {
				@include flexbox;
				flex-wrap: wrap;
				label {
					width: 100px; }
				.input {
					width: calc(100% - 100px); } } }
		#exportbill {
			font-weight: 500;
			text-transform: uppercase; } }
	.cart-coupon {
		@include media-breakpoint-down(sm) {
			margin-bottom: 10px; }
		.input-coupon {
			max-width: 350px;
			@include flexbox;
			align-items: center;
			align-content: center;
			margin-bottom: 5px;
			input {
				width: calc(100% - 110px);
				@include fs(13rem);
				padding: 7px 15px;
				@include bor-rad(5px);
				border: 1px solid darken($white,10%);
				width: 100%;
				color: $main;
				@include transition(all,0.5s);
				&::-webkit-input-placeholder {
					color: inherit;
					opacity: 0.5; }
				&:active, &:focus {
					border-color: lighten($blue,10%);
					box-shadow: 0 0 0 0.2rem color-rgba($blue,0.2); } }
			.btn-submitcoupon {
				width: 105px;
				@include fs(13rem);
				padding: 8px 15px;
				background: $orangegradient;
				color: $white;
				@include bor-rad(5px);
				@include transition(all,0.5s);
				margin-left: 5px;
				&:hover {
					box-shadow: 0px 10px 20px -10px $orange; } } } }
	.cart-payment {
		.payment-method {
			> ul {
				@include flexbox;
				@include bor-rad(5px);
				border: 1px solid lighten($blue,10%);
				@include media-breakpoint-down(sm) {
					display: none; }
				> li.method-select {
					width: calc(100% / 6);
					@include pos(rel,null);
					a {
						display: block;
						padding: 10px; }
					input {
						display: none;
						+ label {
							text-align: center;
							padding-bottom: 20px;
							&:before {
								content: '';
								width: 14px;
								height: 14px;
								@include bor-rad(50px);
								border: 1px solid color-rgba($black,0.1);
								@include pos(abs, null null 10px 50%);
								@include transform(translateX(-50%)); }
							&:after {
								content: '\f00c';
								width: 14px;
								height: 14px;
								background: $blue;
								color: $white;
								@include bor-rad(50px);
								z-index: 1;
								@include flexbox;
								@include fs(12rem);
								align-items: center;
								align-content: center;
								justify-content: center;
								opacity: 0;
								@include pos(abs, null null 10px 50%);
								@include transform(translateX(-50%)); }
							.icon {
								margin-bottom: 10px; }
							img {
								width: 48px;
								filter: grayscale(100%); }
							.name {
								@include fs(12rem);
								line-height: 1.3;
								font-weight: 700;
								@include media-breakpoint-down(lg) {
									font-weight: 500; } } }
						&:checked {
							// + label
							// 	&:after
							// 		opacity: 1
							// 		+media-breakpoint-down(sm)
 } }							// 			opacity: 0
					&:hover {
						a {
							color: $blue; }
						input {
							+ label {
								img {
									filter: grayscale(0%); } } } }
					&.r-tabs-state-active {
						border-right: 1px solid lighten($blue,10%);
						border-left: 1px solid lighten($blue,10%);
						background: color-rgba($blue,0.05);
						a {
							color: $blue; }
						input {
							+ label {
								&:before {
									opacity: 1; }
								img {
									filter: grayscale(0%); } } } }
					&:first-child {
						border-left: none; }
					&:last-child {
						border-right: none; } } }
			.r-tabs-accordion-title {
				display: none;
				margin-bottom: 10px;
				@include media-breakpoint-down(sm) {
					display: block; }
				a {
					display: block;
					@include bor-rad(5px);
					border: 1px solid darken($white,10%);
					padding: 10px; }
				input {
					display: none;
					+ label {
						@include flexbox;
						align-items: center;
						align-content: center;
						@include pos(rel,null);
						padding-left: 20px;
						&:before {
							content: '';
							width: 14px;
							height: 14px;
							@include bor-rad(50px);
							border: 1px solid color-rgba($black,0.1);
							@include pos(abs,50% null null 0);
							@include transform(translateY(-50%)); }
						&:after {
							content: '\f00c';
							width: 14px;
							height: 14px;
							background: $blue;
							color: $white;
							@include bor-rad(50px);
							z-index: 1;
							@include flexbox;
							@include fs(12rem);
							align-items: center;
							align-content: center;
							justify-content: center;
							opacity: 0;
							@include pos(abs,50% null null 0);
							@include transform(translateY(-50%)); }
						.icon {
							width: 32px; }
						.name {
							width: calc(100% - 32px);
							padding-left: 10px;
							@include fs(14rem);
							font-weight: 500; } }
					&:checked {
						// + label
						// 	&:after
						// 		opacity: 1
						// 		+media-breakpoint-down(sm)
 } }						// 			opacity: 0
				&.r-tabs-state-active {
					a {
						border-color: lighten($blue,10%);
						background: color-rgba($blue,0.05); }
					label {
						&:after {
							opacity: 1; } } } }
			.method-content {
				padding: 15px 0;
				@include media-breakpoint-down(sm) {
					padding: 10px 0; }
				.method-img {
					display: none; }
				.content {
					@include fs(14rem);
					@include media-breakpoint-down(sm) {
						@include fs(13rem); } }
				.prepay-select {
					max-width: 300px; } } } }
	.lastview-wrap {} }
.lastview-slick {
	margin: 0px -5px;
	@include media-breakpoint-down(md) {
		margin: 0;
		white-space: nowrap;
		overflow: auto; }
	.item {
		padding: 0px 5px;
		@include media-breakpoint-down(md) {
			padding: 0;
			display: inline-block;
			white-space: normal;
			+ .item {
				margin-left: 10px; } }
		.lastview-item {
			display: block;
			.img {
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				height: 100px;
				img {
					max-width: 80%;
					max-height: 80%; } }
			.name {
				@include fs(12rem);
				font-weight: 500; } } } }

