ul.cardList {
	@include flexbox;
	flex-wrap: wrap;
	list-style: none;
	margin: 0px -5px;
	padding-left: 0;
	padding-top: 10px;
	li {
		width: 20%;
		padding: 0px 5px;
		margin-bottom: 10px;
		@include media-breakpoint-down(sm) {
			width: 33.33%; }
		@include media-breakpoint-down(xs) {
			width: 50%; } }
	label {
		padding: 5px;
		@include bor-rad(5px);
		width: 100%;
		height: 100%;
		display: block;
		text-align: center;
		border: 1px solid darken($white, 10%);
		padding-bottom: 30px;
		@include pos(rel,null);
		&:before {
			content: '';
			width: 14px;
			height: 14px;
			@include bor-rad(50px);
			border: 1px solid color-rgba($black,0.1);
			@include pos(abs, null null 5px 50%);
			@include transform(translateX(-50%)); }
		&:after {
			content: '\f00c';
			width: 14px;
			height: 14px;
			background: $blue;
			color: $white;
			@include bor-rad(50px);
			z-index: 1;
			@include flexbox;
			@include fs(12rem);
			align-items: center;
			align-content: center;
			justify-content: center;
			opacity: 0;
			@include pos(abs, null null 5px 50%);
			@include transform(translateX(-50%)); }
		span {
			@include fs(12rem);
			line-height: 1.2;
			display: block;
			font-weight: 600; } }
	input {
		display: none;
		&:checked {
			+ label {
				border-color: $blue;
				&:after {
					opacity: 1; } } } } }
.modal-repay {
	.module-title {
		@include fs(14rem);
		font-weight: 500;
		margin-bottom: 10px;
		a {
			color: $blue;
			font-weight: 700; } }
	.frm-btn {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: center; }
	.form-wrap {
		ul {
			li {
				margin-bottom: 5px; } }
		.radio {
			input {
				+ label {
					@include fs(14rem); } } } }

	.btn-cancel,.btn-repay {
		display: block;
		text-align: center;
		color: $white;
		@include fs(13rem);
		font-weight: 500;
		line-height: 34px;
		padding: 0px 20px;
		@include bor-rad(50px); }
	.btn-cancel {
		background: linear-gradient(to right,darken($white,40%), darken($white,20%));
		&:hover {
			box-shadow: 0px 10px 20px -10px darken($white,50%); } }
	.btn-repay {
		background: $bluegradient;
		margin-left: 15px;
		&:hover {
			box-shadow: 0px 10px 20px -10px $blue; } } }
.stepcomplete {
	.center-noleftright {
		@include pos(rel,null);
		background: $white;
		@include bor-rad(5px);
		overflow: hidden;
		box-shadow: 0px 5px 20px color-rgba($black,0.05);
		padding: 15px;
		margin-bottom: 20px;
		@include media-breakpoint-down(md) {
			margin: 0px -15px 10px;
			padding: 15px 0;
			@include bor-rad(0);
			box-shadow: none; }
		.payment-success {
			.cart-info {
				.info-overview {
					@include media-breakpoint-down(sm) {
						flex-wrap: wrap;
						padding: 10px 0; }
					.info-col {
						@include media-breakpoint-down(sm) {
							width: 100%;
							text-align: left !important;
							padding: 0px 15px; } } } } }
		.cart-btnwrap {
			@include flexbox;
			align-items: center;
			align-content: center;
