.menu {
	// display: none
	width: 100%;
	@include media-breakpoint-down(lg) {
		@include pos(fix, 0 null 0 0);
		overflow: auto;
		z-index: 1000;
		background: $white;
		width: 330px;
		@include transform(translateX(-100%));
		@include transition(all,0.5s);
		&.open {
			@include transform(translateX(0)); } }
	@include media-breakpoint-down(sm) {
		width: 280px; }
	.menu-banner {
		display: none;
		@include media-breakpoint-down(lg) {
			display: block; } }
	.menu-login {
		display: none;
		@include media-breakpoint-down(lg) {
			@include flexbox;
			align-items: center;
			align-content: center;
			padding: 10px 20px;
			background: $bluegradient;
			.avt, img {
				width: 40px; }
			ul {
				padding-left: 10px;
				@include flexbox;
				flex-wrap: wrap;
				li {
					display: block;
					width: 100%;
					&:nth-child(1) {
						order: 2; }
					&:nth-child(2) {
						order: 1; } }
				a {
					display: block;
					@include fs(11rem);
					line-height: 1.5;
					color: $white;
					// opacity: 0.8
					text-transform: uppercase;
					&:after {
						content: '\f105';
						margin-left: 5px; } }
				.btn-login {
					@include fs(13rem);
					font-weight: 700;
					opacity: 1; } } } }
	.menu-wrap {
		@include media-breakpoint-down(lg) {
			padding: 10px 0; } }
	ul.menu-link {
		@include flexbox;
		flex-wrap: wrap;
		align-items: center;
		align-content: center;
		&.menu-pc {
			@include media-breakpoint-down(lg) {
				display: none; } }
		&.menu-mobile {
			display: none;
			@include media-breakpoint-down(lg) {
				@include flexbox;
				align-items: center;
				align-content: center; } }
		li {
			@include media-breakpoint-down(lg) {
				width: 100%; }
			+ li {
				margin-left: 5px;
				@include media-breakpoint-down(lg) {
					margin-left: 0; } }
			a {
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				line-height: 20px;
				@include fs(14rem);
				padding: 10px 20px;
				color: $main;
				@include pos(rel,null);
				@include media-breakpoint-down(lg) {
					color: darken($white,60%);
					padding: 10px 15px;
					justify-content: flex-start; }
				&:after {
					content: '';
					@include pos(abs, null 0 0 0);
					height: 3px;
					background: $bluegradient;
					z-index: 1;
					opacity: 0;
					@include transition(all,0.5s); }
				.material-icons, > img {
					margin-right: 5px;
					@include fs(18rem);
					@include media-breakpoint-down(lg) {
						@include fs(24rem);
						margin-right: 10px; } }
				span {
					font-weight: 700;
					@include media-breakpoint-down(lg) {
						font-weight: normal; } } }
			&.hardware {
				flex: auto;
				a {
					background: $orangegradient;
					color: $white;
					animation: rgb 10s infinite; }
				&:hover, &.active {
					a {
						background: $orangegradient;
						color: $white; } } }
			@mixin menuactive($color,$bg) {
				@include media-breakpoint-up(xl) {
					&:hover {
						a {
							color: $color; } }
					&.active {
						a {
							color: $color;
							&:after {
								background: $bg;
								opacity: 1; } } } } }
			@include menuactive($blue,$bluegradient);
			&:nth-child(2) {
				@include menuactive($amber,$ambergradient); }
			&:nth-child(3) {
				@include menuactive($pink,$pinkgradient); }
			&:nth-child(4) {
				@include menuactive($green,$greengradient); }
			&:nth-child(5) {
				@include menuactive($red,$redgradient); }
			&:nth-child(6) {
				@include menuactive($cyan,$cyangradient); }
			&:nth-child(7) {
				@include menuactive($purple,$purplegradient); }
			&:nth-child(8) {
				@include menuactive($lightgreen,$lightgreengradient); }
			// &.hardware
 } }			/// +menuactive($redgradient)
	.menu-contact {
		display: none;
		margin: 0px 20px;
		border-top: 1px solid darken($white,5%);
		padding: 20px 0;
		@include media-breakpoint-down(lg) {
			display: block; }
		.company {
			@include fs(14rem);
			font-weight: 700;
			text-transform: uppercase;
			margin-bottom: 10px; }
		.contact-info {
			li {
				padding-left: 25px;
				@include pos(rel,null);
				@include fs(14rem);
				line-height: 1.4;
				color: darken($white,60%);
				margin-bottom: 5px;
				.material-icons {
					@include pos(abs, 1px null null 0);
					@include fs(18rem); } } } } }
