.product-item {
	height: 100%;
	@include pos(rel,null);
	@include media-breakpoint-down(lg) {
		height: 100%; }
	.btn_compare {
		// display: none
		@include pos(abs,0 null 0 0);
		right: 0;
		@include fs(0);
		visibility: hidden;
		opacity: 0;
		z-index: 10;
		&:before {
			content: '\f00c';
			display: block;
			width: 30px;
			height: 30px;
			line-height: 30px;
			text-align: center;
			border: 1px solid darken($white,10%);
			@include bor-rad(50px);
			@include fs(16rem);
			margin-top: 10px;
			margin-left: 10px;
			color: darken($white,20%); }
		&.active {
			&:before {
				// border-color: color-rgba($white,0)
				background: $purplegradient;
				color: $white; } } } }

.product-col {
	display: block;
	height: 100%;
	padding: 10px;
	// border: 1px solid transparent
	box-shadow: 0px 10px 20px -10px color-rgba($black,0.1);
	@include bor-rad(5px);
	@include transition(all,0.5s);
	@include media-breakpoint-down(lg) {
		padding: 5px; }
	@include media-breakpoint-down(sm) {
		padding: 10px; }
	&:hover {
		box-shadow: 0px 10px 20px -10px color-rgba($black,0.3); }
	figure {
		@include pos(rel,null);
		.product-img {
			@include flexbox;
			margin: 0px -10px;
			height: 200px;
			justify-content: center;
			align-items: center;
			align-content: center;
			overflow: hidden;
			@include media-breakpoint-down(lg) {
				margin: 0px -5px;
				height: 180px; }
			@include media-breakpoint-down(sm) {
				height: 160px;
				margin: 0px -10px; }
			picture {
				width: 100%;
				height: 100%;
				@include flexbox;
				justify-content: center;
				align-items: center;
				align-content: center; }
			img {
				// max-width: 100%
				// max-height: 100%
				// +media-breakpoint-down(sm)
				max-width: 90%;
				max-height: 90%; } }
		figcaption {
			padding-top: 10px;
			.product-name {
				@include fs(14rem);
				line-height: 1.3;
				font-weight: 500;
				color: $black;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
				overflow: hidden;
				text-overflow: ellipsis;
				height: 35px;
				@include transition(all,0.5s);
				a {
					color: $black; } }
			.product-price {
				margin-top: 5px;
				// +pos(rel,null)
				.promotion {
					@include pos(abs, 2px 0 null null);
					@include fs(11rem);
					font-weight: 600;
					color: $white;
					padding: 0px 5px;
					@include bor-rad(3px);
					&.off {
						background: linear-gradient(to right, $orange, $deeporange); } }
				.new {
					@include fs(14rem);
					line-height: 1;
					font-weight: 700;
					color: $red;
					@include media-breakpoint-down(lg) {
 } }						// +fs(12rem)
				.old {
					font-weight: 500;
					color: $bluegrey;
					@include fs(13rem);
					line-height: 1;
					color: darken($white,40%);
					text-decoration: line-through; } }
			.product-rating {
				@include flexbox;
				align-items: center;
				align-content: center;
				padding: 5px 0;
				flex-wrap: wrap;
				@include media-breakpoint-down(lg);
				.br-wrapper {
					margin-right: 5px; }
				span {
					@include fs(12rem);
					color: darken($white,40%); } }
			.product-des {
				@include fs(12rem);
				line-height: 1.4;
				white-space: normal;
				margin-top: 5px;
				color: $main;
				ul {
					list-style-type: disc;
					padding-left: 20px;
					li {} } } } }
	&:hover {
		@include media-breakpoint-up(xl) {
			// border: 1px solid darken($white,10%)
			figure {
				figcaption {
					.product-name {
						color: $blue; } } } } } }

