footer {
	background: $white;
	border-top: 1px solid darken($white,10%);
	.footer-top {
		background: $white;
		padding: 40px 0 0px;
		@include media-breakpoint-down(lg) {
			padding: 20px 0px 0px; } }
	.footer-wrap {
		@include flexbox;
		flex-wrap: wrap;
		margin: 0px -10px;
		.item {
			width: calc(100% / 5);
			padding: 0px 10px;
			@include media-breakpoint-down(lg) {
				width: 100%;
				display: none; }
			&:first-child {
				@include media-breakpoint-down(lg) {
					display: block; } } } }
	.footer-col {
		margin-bottom: 40px;
		@include media-breakpoint-down(lg) {
			margin-bottom: 20px; } }
	.sitelogo {
		margin-bottom: 10px;
		@include media-breakpoint-down(lg) {
			text-align: center;
			margin-bottom: 15px; }
		img {
			height: 30px; } }
	.site-feature {
		display: none;
		@include bor-rad(5px);
		background: $white;
		margin-bottom: 20px;
		border: 1px solid darken($white,15%);
		@include media-breakpoint-down(lg) {
			@include flexbox;
			background: darken($white,5%); }
		.feature {
			width: 33.33%;
			text-align: center;
			padding: 10px;
			@include media-breakpoint-down(sm) {
				padding: 5px; }
			+ .feature {
				border-left: 1px solid darken($white,15%); }
			.icon {}
			.des {
				@include fs(12rem);
				line-height: 1.1; } } }
	.company {
		@include fs(16rem);
		line-height: 1.3;
		font-weight: 700;
		// text-transform: uppercase
		margin-bottom: 5px;
		@include media-breakpoint-down(lg) {
			text-align: center;
			text-transform: uppercase;
			margin-bottom: 15px; } }
	.contact-info {
		@include media-breakpoint-down(lg) {
			text-align: center; }
		li {
			@include fs(14rem);
			// color: $main
			margin-bottom: 5px;
			@include media-breakpoint-down(sm) {
				margin-bottom: 0; }
			a {
				// color: $main
				&:hover {
					color: $blue; } } } }
	.footer-title {
		@include fs(15rem);
		font-weight: 500;
		text-transform: uppercase;
		margin-bottom: 15px; }
	.footer-link {
		a {
			display: block;
			@include fs(14rem);
			// color: $main
			margin-bottom: 5px;
			&:hover {
				color: $blue; } } }
	.payment-icon {
		@include flexbox;
		flex-wrap: wrap;
		align-items: center;
		align-content: center;
		margin-bottom: 5px;
		.icon {
			margin-right: 10px;
			margin-bottom: 10px; } }
	.bct {}
	.social {
		@include flexbox;
		align-items: center;
		align-content: center;
		a {
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center;
			width: 32px;
			height: 32px;
			margin-right: 15px; } }
	.footer-bottom {
		border-top: 1px solid darken($white,10%);
		.copyright {
			padding: 15px 0;
			@include fs(14rem);
			text-align: center;
			@include media-breakpoint-down(md) {
				@include fs(13rem); }
			@include media-breakpoint-down(sm) {
				@include fs(12rem); } } } }


