.home-navs {
	@include pos(rel,null);
	.nav-tabs {
		margin: 0;
		padding: 0 0 15px;
		border: none;
		@include media-breakpoint-down(sm) {
			display: block;
			overflow: auto;
			white-space: nowrap; }
		.nav-item {
			margin: 0;
			padding: 0;
			@include media-breakpoint-down(sm) {
				display: inline-block; }
			+ .nav-item {
				@include media-breakpoint-down(sm) {
					margin-left: 20px; } }
			.nav-link {
				display: block;
				margin: 0;
				padding: 0;
				background: none;
				border: none;
				@include bor-rad(50px);
				@include fs(16rem);
				text-transform: capitalize;
				font-weight: 700;
				padding: 8px 25px;
				color: $main;
				@include pos(rel,null);
				@include media-breakpoint-down(sm) {
					padding: 0;
					text-align: center;
					color: darken($white,40%); }
				&:hover {
					color: $blue; }
				&.active {
					margin: 0;
					border: none;
					color: $white;
					background: $bluegradient;
					box-shadow: 0px 10px 20px -10px $blue;
					@include media-breakpoint-down(sm) {
						background: none;
						box-shadow: none;
						color: $main; }
					&:after {
						width: 100%; } } } } } }
.search-navs {
	.nav-tabs {
		margin: 0;
		padding: 0;
		border: none;
		@include bor-rad(0);
		border-bottom: 1px solid darken($white,10%);
		@include media-breakpoint-down(lg) {
			justify-content: center; }
		.nav-item {
			margin: 0;
			padding: 0;
			border: none;
			@include media-breakpoint-down(lg) {
				width: 50%; }
			.nav-link {
				margin: 0;
				padding: 0;
				border: none;
				@include bor-rad(0);
				@include fs(16rem);
				font-weight: 700;
				padding: 12px 30px;
				text-align: center;
				text-transform: none;
				color: darken($white,40%);
				@include pos(rel,null);
				@include media-breakpoint-down(lg) {
					@include fs(14rem);
					line-height: 20px;
					padding: 10px 30px;
					text-transform: uppercase; }
				&:after {
					content: '';
					@include pos(abs, null 0 -1px 0);
					z-index: 3;
					height: 3px;
					background: $bluegradient;
					opacity: 0; }
				&.active {
					color: $main;
					&:after {
						opacity: 1; } } } } } }

.product-tabs {
	margin: 0;
	padding: 0 0 10px;
	border: none;
	border-bottom: 1px solid darken($white,10%);
	@include media-breakpoint-down(md) {
		display: block;
		white-space: nowrap;
		overflow: auto; }
	.nav-item {
		margin: 0;
		padding: 0;
		border: none;
		@include media-breakpoint-down(md) {
			display: inline-block !important;
			vertical-align: top; }
		+ .nav-item {
			margin-left: 30px;
			@include media-breakpoint-down(md) {
				margin-left: 20px; } }
		.nav-link {
			margin: 0;
			padding: 0;
			border: none;
			background: none;
			@include fs(16rem);
			font-weight: 500;
			text-transform: uppercase;
			color: darken($white,40%);
			&.active {
				color: $main; } } } }
