.product-dealtime {
	@include fs(13rem);
	text-align: center;
	color: darken($white,60%);
	margin-bottom: 20px;
	time {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: center;
		margin: 0px -5px;
		span {
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center;
			@include pos(rel,null);
			width: 36px;
			height: 36px;
			@include bor-rad(5px);
			border: 1px solid darken($white,3%);
			background: linear-gradient(color-rgba($black,0.03),color-rgba($black,0));
			box-shadow: 0px 3px 1px 0 color-rgba($black,0.1);
			@include fs(14rem);
			font-weight: 600;
			margin: 0px 5px;
			color: $red;
			@include media-breakpoint-down(xs) {
				width: 30px;
				height: 30px; }
			label {
				@include pos(abs, null 0 -20px 0);
				text-align: center;
				@include fs(11rem);
				font-weight: 500;
				color: $bluegrey; } } } }
.hot-deal {
	@include media-breakpoint-down(xs) {
		margin: -10px !important; }
	.hotdeal-item {
		flex: 0 0 20%;
		max-width: 20%;
		@include media-breakpoint-down(lg) {
			flex: 0 0 25%;
			max-width: 25%; }
		@include media-breakpoint-down(md) {
			flex: 0 0 33.33%;
			max-width: 33.33%; }
		@include media-breakpoint-down(xs) {
			flex: 0 0 50%;
			max-width: 50%; }
		.product-col {
			figcaption {
				@include flexbox;
				flex-wrap: wrap;
				flex-direction: column;
				text-align: center;
				.product-rating {
					justify-content: center; } } } } }
