.installment-page {
	.installment-title {
		@include flexbox;
		align-items: center;
		align-content: center;
		border-bottom: 1px solid darken($white,10%);
		padding-bottom: 10px;
		margin-bottom: 10px;
		.icon {
			@include flexbox;
			align-items: center;
			align-content: center;
			margin-right: 10px; }
		span {
			@include fs(14rem);
			font-weight: 700;
			text-transform: uppercase; } }
	.installment-product {
		background: $white;
		@include bor-rad(5px);
		overflow: hidden;
		box-shadow: 0px 5px 20px color-rgba($black,0.05);
		max-width: 900px;
		margin: 15px auto;
		padding: 15px;
		@include media-breakpoint-down(sm) {
			margin: 0px -15px 10px;
			box-shadow: none;
			@include bor-rad(0); }
		figure {
			@include flexbox; }
		.product-img {
			width: 100px;
			height: 100px;
			@include bor-rad(5px);
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center;
			@include media-breakpoint-down(sm) {
				width: 60px;
				height: 60px; } }
		figcaption {
			width: calc(100% - 100px);
			padding-left: 15px;
			@include media-breakpoint-down(sm) {
				width: calc(100% - 60px);
				padding-left: 10px; }
			.product-name {
				@include fs(14rem);
				line-height: 1.3;
				font-weight: 500;
				color: $black;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
				overflow: hidden;
				text-overflow: ellipsis;
				@include transition(all,0.5s);
				a {
					color: $black; } }
			.product-price {
				margin-top: 5px;
				@include pos(rel,null);
				.promotion {
					@include pos(abs, 2px 0 null null);
					@include fs(11rem);
					font-weight: 600;
					color: $white;
					padding: 0px 5px;
					@include bor-rad(3px);
					&.off {
						background: linear-gradient(to right, $orange, $deeporange); } }
				.new {
					@include fs(14rem);
					line-height: 1;
					font-weight: 700;
					color: $red;
					@include media-breakpoint-down(lg) {
 } }						// +fs(12rem)
				.old {
					@include fs(10rem);
					line-height: 1;
					color: darken($white,40%);
					text-decoration: line-through; } } } }
	.installment-selection {
		background: $white;
		@include bor-rad(5px);
		overflow: hidden;
		box-shadow: 0px 5px 20px color-rgba($black,0.05);
		max-width: 900px;
		margin: 15px auto;
		padding: 15px;
		@include media-breakpoint-down(sm) {
			margin: 0px -15px 10px;
			box-shadow: none;
			@include bor-rad(0); }
		.installment-nav {
			margin: 0;
			padding: 0;
			border: none;
			@include bor-rad(5px);
			overflow: hidden;
			border: 1px solid lighten($blue,10%);
			.nav-item {
				width: 50%;
				margin: 0;
				padding: 0;
				border: none;
				background: none;
				@include bor-rad(0);
				&:first-child {
					.nav-link {
						border-left: none; } }
				&:last-child {
					.nav-link {
						border-right: none; } }
				.nav-link {
					@include flexbox;
					align-items: center;
					align-content: center;
					justify-content: center;
					margin: 0;
					padding: 10px;
					border: none;
					@include bor-rad(0);
					border-left: 1px solid transparent;
					border-right: 1px solid transparent;
					.icon {
						width: 32px;
						height: 32px;
						@include flexbox;
						align-items: center;
						align-content: center;
						justify-content: center;
						@include media-breakpoint-down(sm) {
							width: 24px;
							height: 24px; }
						img {
							max-width: 100%;
							max-height: 100%; } }
					.name {
						padding-left: 10px;
						@include fs(14rem);
						font-weight: 500;
						text-transform: uppercase;
						@include media-breakpoint-down(sm) {
							width: calc(100% - 24px);
							padding-left: 0;
							@include fs(13rem);
							line-height: 1.3;
							text-align: center; } }
					&.active {
						background: color-rgba($blue,0.05);
						border-color: lighten($blue,10%);
						color: $blue; } } } }
		.tab-content {
			padding: 15px 0;
			@include pos(rel,null);
			.btn-viewdetail {
				display: block;
				@include pos(abs, 15px 0px null null);
				@include fs(14rem);
				font-weight: 500;
				color: $blue;
				@include media-breakpoint-down(sm) {
					@include pos(static,null);
					margin-bottom: 10px; }
				&:hover {
					color: $orange; } } }
		.installment-option {
			margin-bottom: 15px;
			.option-list {
				@include flexbox;
				flex-wrap: wrap;
				padding-left: 0;
				list-style-type: none;
				@include media-breakpoint-down(sm) {
					display: block;
					overflow: auto;
					white-space: nowrap; }
				li {
					@include media-breakpoint-down(sm) {
						display: inline-block;
						vertical-align: top; }
					+ li {
						margin-left: 10px;
						@include media-breakpoint-down(sm) {
							margin-left: 5px; } }
					a {
						display: block;
						@include fs(14rem);
						font-weight: 700;
						text-align: center;
						line-height: 34px;
						@include bor-rad(5px);
						border: 1px solid darken($white,10%);
						padding: 0px 20px;
						&.active {
							border-color: lighten($blue,10%);
							background: color-rgba($blue,0.05);
							color: $blue; } }
					&.active {
						a {
							border-color: lighten($blue,10%);
							background: color-rgba($blue,0.05);
							color: $blue; } } } }
			.form-row {}
			.form-group {
				padding: 0px 5px;
				@include media-breakpoint-down(sm) {
					width: 50%; } } }
		.installment-table {
			table {
				@include bor-rad(5px);
				overflow: hidden;
				border: 1px solid darken($white,10%);
				tr {
					&:nth-child(odd) {
						background: darken($white,3%); } }
				th, td {
					@include fs(14rem);
					font-weight: normal;
					padding: 10px;
					width: 50%; }
				th {
					font-weight: 500;
					+ th {
						border-left: 1px solid darken($white,10%); } }
				td {
					border-left: 1px solid darken($white,10%); }
				.prepay-select {
					border: 1px solid darken($white,10%);
					@include bor-rad(3px);
					vertical-align: middle;
					&:active, &:focus {
						border-color: lighten($blue,10%);
						box-shadow: 0 0 0 0.2rem color-rgba($blue,0.2); } } } } }
	.bank-list {
		@include flexbox;
		flex-wrap: wrap;
		margin: 0px -5px;
		.item {
			width: calc(100% / 6);
			padding: 0px 5px;
			margin-bottom: 10px;
			@include media-breakpoint-down(sm) {
				width: calc(100% / 5); }
			@include media-breakpoint-down(xs) {
				width: calc(100% / 3); } }
		.bank-item {
			height: 60px;
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center;
			@include bor-rad(5px);
			border: 1px solid darken($white,10%);
			@include pos(rel,null);
			&:before {
				content: '\f00c';
				width: 14px;
				height: 14px;
				background: $blue;
				color: $white;
				@include bor-rad(50px);
				z-index: 1;
				@include flexbox;
				@include fs(12rem);
				align-items: center;
				align-content: center;
				justify-content: center;
				opacity: 0;
				@include pos(abs, null 5px 5px null); }
			img {
				max-width: 80%;
				max-height: 80%;
				margin: 0; }
			&:hover, &.active {
				border-color: lighten($blue,10%);
				background: color-rgba($blue,0.05); }
			&.active {
				&:before {
					opacity: 1; } } } }
	.btn-buy {
		margin-top: 15px;
		@extend .btn-main;
		text-transform: uppercase; }
	.confirmation-table {
		table {
			@include bor-rad(5px);
			overflow: hidden;
			border: 1px solid darken($white,10%);
			tr {
				&:nth-child(odd) {
					background: darken($white,3%); } }
			th, td {
				@include fs(14rem);
				font-weight: normal;
				padding: 10px;
				width: 50%; }
			th {
				font-weight: 500;
				+ th {
					border-left: 1px solid darken($white,10%); } }
			th[colspan="2"] {
				background: $bluegradient;
				color: $white; }
			td {
				border-left: 1px solid darken($white,10%); } } }
	.installment-form {
		background: $white;
		@include bor-rad(5px);
		overflow: hidden;
		box-shadow: 0px 5px 20px color-rgba($black,0.05);
		max-width: 900px;
		margin: 15px auto;
		padding: 15px;
		@include media-breakpoint-down(sm) {
			margin: 0px -15px 10px;
			box-shadow: none;
			@include bor-rad(0); }
		.form-wrap {
			.form-group {
				@include flexbox;
				flex-wrap: wrap;
				label {
					width: 100px; }
				.input {
					width: calc(100% - 100px); } } }
		.input-coupon {
			max-width: 350px;
			@include flexbox;
			align-items: center;
			align-content: center;
			margin-bottom: 5px;
			// margin-left: 100px
			// + .form-des
			// 	margin-left: 100px
			input {
				width: calc(100% - 85px);
				@include fs(13rem);
				padding: 7px 15px;
				@include bor-rad(5px);
				border: 1px solid darken($white,10%);
				color: $main;
				@include transition(all,0.5s);
				&::-webkit-input-placeholder {
					color: inherit;
					opacity: 0.5; }
				&:active, &:focus {
					border-color: lighten($blue,10%);
					box-shadow: 0 0 0 0.2rem color-rgba($blue,0.2); } }
			.btn-submitcoupon {
				width: 80px;
				@include fs(13rem);
				padding: 8px 10px;
				background: $orangegradient;
				color: $white;
				@include bor-rad(5px);
				@include transition(all,0.5s);
				margin-left: 5px;
				&:hover {
					box-shadow: 0px 10px 20px -10px $orange; } } } } }
