.brand-page {
	background: $white;
	@include bor-rad(5px);
	overflow: hidden;
	box-shadow: 0px 5px 20px color-rgba($black,0.05);
	margin: 15px 0;
	padding: 15px;
	.btn-loadmore {
		width: 150px;
		margin: 15px auto 0;
		@include fs(14rem);
		font-weight: 500;
		color: $white;
		background: $bluegradient;
		@include bor-rad(50px);
		line-height: 36px;
		display: block;
		padding: 0px 30px;
		text-align: center;
		@include media-breakpoint-down(sm) {
			margin: 15px auto;
			@include fs(13rem);
			line-height: 36px; }
		&:before {
			content: '\f055';
			font-family: 'fontawesome';
			margin-right: 10px; }
		&:hover {
			box-shadow: 0px 10px 20px -10px $blue; } }
	@include media-breakpoint-down(md) {
		margin: 0 -15px 10px;
		@include bor-rad(0);
		box-shadow: none; }
	.brand-nav {
		margin-bottom: 30px;
		margin: 0px -15px 20px;
		border-bottom: 1px solid darken($white,10%);
		background: $white;
		ul {
			display: block;
			overflow: auto;
			white-space: nowrap;
			padding: 0px 15px;
			li {
				display: inline-block;
				vertical-align: top;
				+ li {
					margin-left: 5px; }
				a {
					display: block;
					@include fs(14rem);
					font-weight: 700;
					color: $main;
					line-height: 36px;
					padding: 0px 20px;
					@include bor-rad(50px);
					@include pos(rel,null);
					@include bor-rad(0);
					line-height: 40px;
					padding: 0px 15px;
					&:after {
						content: '';
						@include pos(abs, null 0 0 0);
						height: 3px;
						background: $bluegradient;
						z-index: 1;
						opacity: 0; } }
				&.active {
					a {
						background: none;
						color: $main;
						&:after {
							opacity: 1; } } } } } }
	.brand-list {
		.col-custom {
			@include media-breakpoint-up(xl) {
				flex: 0 0 20%;
				max-width: 20%; } } }
	.brand-item {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: center;
		height: 80px;
		margin-bottom: 20px;
		img {
			max-width: 70%;
			max-height: 70%;
			@include transition(all,0.5s); }
		&:hover {
			img {
				@include transform(scale3d(1.1,1.1,1)); } } }
	.brand-header {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: space-between;
		margin-bottom: 15px;
		@include media-breakpoint-down(sm) {
			justify-content: center; }
		.brand-logo {
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center;
			height: 50px;
			a {} }
		.brand-menu {
			@include media-breakpoint-down(sm) {
				display: none; }
			ul {
				@include flexbox;
				align-items: center;
				align-content: center;
				li {
					+ li {
						margin-left: 20px; }
					a {
						display: block;
						@include fs(14rem);
						font-weight: 500;
						text-transform: uppercase;
						color: $main; }
					&:hover {
						a {
							color: $blue; } } } } } }
	.brand-banner {
		margin-bottom: 20px;
		.slick-prev, .slick-next {
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			z-index: 1;
			@include fs(0);
			color: $white;
			text-shadow: 0px 1px 5px color-rgba($black,0.1);
			opacity: 0;
			@include transition(all,0.5s);
			&:before {
				content: '';
				font-family: 'linearicons-free';
				@include fs(28rem); } }
		.slick-prev {
			left: 0px;
			&:before {
				content: '\e875'; } }
		.slick-next {
			right: 0px;
			&:before {
				content: '\e876'; } }
		&:hover {
			.slick-prev, .slick-next {
				opacity: 1; }
			.slick-prev {
				left: 20px; }
			.slick-next {
				right: 20px; } }
		.slick-dots {
			@include pos(abs, null 0 10px 0);
			margin: 10px 0;
			text-align: center;
			li {
				display: inline-block;
				width: 10px;
				height: 10px;
				@include bor-rad(50px);
				background: darken($white,10%);
				&.slick-active {
					background: $bluegradient; }
				button {
					display: none; }
				+ li {
					margin-left: 5px; } } }
		img {
			width: 100%; } }
	.brand-section {
		border-bottom: 1px solid darken($white,10%);
		margin-bottom: 30px; }
	.brand-category {
		ul {
			@include flexbox;
			flex-wrap: wrap;
			justify-content: center;
			li {
				width: 20%;
				margin-bottom: 30px;
				// +media-breakpoint-down(md)
				// 	width: 25%
				@include media-breakpoint-down(sm) {
					width: 33.33%; }
				@include media-breakpoint-down(xs) {
					width: 50%; }
				.category {
					text-align: center;
					.img {
						@include flexbox;
						align-items: center;
						align-content: center;
						justify-content: center;
						width: 80px !important;
						height: 80px !important;
						background: $white;
						box-shadow: 0px 10px 20px -10px color-rgba($black,0.3);
						@include bor-rad(50px);
						margin-bottom: 5px;
						@include fs(0);
						img {
							max-width: 32px !important;
							max-height: 32px !important;
							filter: brightness(0%);
							opacity: 0.5; } }
					.name {
						@include fs(13rem);
						line-height: 1.3;
						font-weight: 500; }
					&:hover {
						.img {
							background: $bluegradient;
							img {
								filter: brightness(200%);
								opacity: 1; } }
						.name {
							color: $blue; } } } } } }
	.product-slick {
		margin-bottom: 30px;
		.slick-prev, .slick-next {
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			z-index: 3;
			@include fs(0);
			color: $main;
			text-shadow: 0px 1px 5px color-rgba($black,0.1);
			opacity: 0;
			@include transition(all,0.5s);
			&:before {
				content: '';
				font-family: 'linearicons-free';
				@include fs(28rem); } }
		.slick-prev {
			left: 0px;
			&:before {
				content: '\e875'; } }
		.slick-next {
			right: 0px;
			&:before {
				content: '\e876'; } }
		&:hover {
			.slick-prev, .slick-next {
				opacity: 1; }
			.slick-prev {
				left: 20px; }
			.slick-next {
				right: 20px; } } } }
.brand-hotsale {
	margin-bottom: 30px; }
