.product-detail-page {
	.product-page {
		background: none;
		box-shadow: none;
		padding: 0;
		margin: 0; } }
.product-page {
	.product-intro {
		background: $white;
		padding: 15px;
		@include bor-rad(5px);
		box-shadow: 0px 5px 20px color-rgba($black,0.05);
		margin: 15px 0;
		@include pos(rel,null);
		@include media-breakpoint-down(md) {
			margin: 0px -15px 10px;
			box-shadow: none;
			@include bor-rad(0); } }
	.product-slide-wrap {
		@include pos(rel,null);
		@include flexbox;
		flex-wrap: wrap;
		justify-content: flex-end;
		@include media-breakpoint-down(md) {
			margin-bottom: 30px; }
		@include media-breakpoint-down(sm) {
			margin-bottom: 0; }
		.promotion {
			position: absolute;
			top: 5px;
			left: 80px;
			font-size: 0.875rem;
			font-weight: 600;
			color: #fff;
			padding: 3px 15px;
			border-radius: 5px;
			z-index: 5;
			@include media-breakpoint-down(sm) {
				left: 0;
				font-size: 0.75rem;
				padding: 2px 10px; }
			@include media-breakpoint-down(xs) {
				font-size: 0.6875rem;
				padding: 3px 10px; }
			&.off {
				background: linear-gradient(to right, #FF9800, #FF5722);
				box-shadow: 0px 5px 20px -5px #FF5722; } } }
	.product-nav {
		width: 70px;
		@include pos(abs, 0 null null 0);
		@include media-breakpoint-down(lg) {
			width: 60px; }
		@include media-breakpoint-down(sm) {
			width: 50px;
			display: none; }
		.slick-prev, .slick-next {
			width: 100%;
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center;
			@include fs(0);
			background: darken($white,3%);
			height: 30px;
			@include bor-rad(5px);
			@include transition(all,0.5s);
			&:before {
				content: '';
				@include fs(16rem); }
			&:hover {
				background: darken($white,5%);
				box-shadow: 0px 10px 20px -10px color-rgba($black,0.1); } }
		.slick-prev {
			&:before {
				content: '\f106'; } }
		.slick-next {
			&:before {
				content: '\f107'; } }
		.slick-slide {
			border: none;
			.item {
				width: 70px;
				cursor: pointer;
				@include media-breakpoint-down(lg) {
					width: 60px; }
				@include media-breakpoint-down(sm) {
					width: 50px; }
				.img {
					width: 70px;
					height: 70px;
					@include bor-rad(5px);
					@include flexbox;
					align-items: center;
					align-content: center;
					justify-content: center;
					border: 1px solid transparent;
					@include bor-rad(5px);
					overflow: hidden;
					@include media-breakpoint-down(lg) {
						width: 60px;
						height: 60px; }
					@include media-breakpoint-down(sm) {
						width: 50px;
						height: 50px; }
					@include transition(all,0.5s);
					img {
						max-width: 80%;
						max-height: 80%; } } }
			&.slick-current {
				.item {
					.img {
						border-color: $blue; } } } } }
	.product-slide {
		width: 390px;
		@include media-breakpoint-down(lg) {
			width: calc(100% - 60px); }
		@include media-breakpoint-down(sm) {
			// width: calc(100% - 50px)
			width: 100%;
			.slick-prev, .slick-next {
				@include pos(abs, 50% null null null);
				@include transform(translateY(-50%));
				z-index: 3;
				@include fs(0);
				color: $main;
				text-shadow: 0px 1px 5px color-rgba($black,0.1);
				opacity: 0;
				@include transition(all,0.5s);
				&:before {
					content: '';
					font-family: 'linearicons-free';
					@include fs(24rem); } }
			.slick-prev {
				left: 0px;
				&:before {
					content: '\e875'; } }
			.slick-next {
				right: 0px;
				&:before {
					content: '\e876'; } }
			&:hover {
				.slick-prev, .slick-next {
					opacity: 0.5; }
				.slick-prev {
					left: 0px; }
				.slick-next {
					right: 0px; } } }
		.slick-dots {
			// +pos(abs, null 0 0 0)
			margin: 10px 0;
			text-align: center;
			li {
				display: inline-block;
				width: 10px;
				height: 10px;
				@include bor-rad(50px);
				background: darken($white,10%);
				&.slick-active {
					background: $bluegradient; }
				button {
					display: none; }
				+ li {
					margin-left: 5px; } } }
		.item {
			.img {
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				width: 390px;
				height: 390px;
				@include media-breakpoint-down(lg) {
					width: 100%;
					height: 325px; }
				@include media-breakpoint-down(md) {
					height: 500px; }
				@include media-breakpoint-down(sm) {
					height: 300px; }
				img {
					max-width: 100%;
					max-height: 100%; } } } }
	.slide-des {
		@include fs(13rem);
		width: 100%;
		text-align: center;
		color: darken($white,40%);
		font-style: italic;
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: center;
		margin-top: 10px;
		margin-left: calc(100% - 390px);
		@include media-breakpoint-down(lg) {
			margin-left: 0;
			width: calc(100% - 60px); }
		@include media-breakpoint-down(sm) {
			width: 100%;
			display: none; }
		.material-icons {
			@include fs(18rem); } }
	.product-info {
		@include pos(rel,null);
		.row {
			margin: 0px -10px;
			@include pos(static,null);
			.col-12 {
				padding: 0px 10px;
				@include pos(static,null); } }
		.product-header {
			border-bottom: 1px dashed darken($white,10%);
			padding-bottom: 15px;
			margin-bottom: 15px;
			@include media-breakpoint-down(sm) {
				padding-bottom: 10px;
				margin-bottom: 10px; } }
		.product-name {
			@include fs(24rem);
			line-height: 1.3;
			font-weight: 700;
			@include media-breakpoint-down(md) {
				padding-right: 50px; }
			@include media-breakpoint-down(sm) {
				@include fs(16rem); } }
		.product-rating {
			@include flexbox;
			align-items: center;
			align-content: center;
			margin-top: 5px;
			.link {
				@include fs(14rem);
				line-height: 1;
				color: darken($white,40%);
				margin-left: 10px;
				&:hover {
					color: $blue; } } }
		.product-brand, .product-id {
			display: inline-block;
			@include fs(13rem);
			line-height: 1;
			color: darken($white,40%);
			margin-top: 5px;
			margin-right: 10px;
			a {
				color: $main;
				&:hover {
					color: $blue; } } }
		.product-status {
			@include fs(13rem);
			line-height: 1;
			color: darken($white,40%); }
		.product-price {
			margin-bottom: 10px;
			padding-bottom: 10px;
			border-bottom: 1px dashed darken($white,10%);
			.new {
				@include fs(18rem);
				color: $red;
				font-weight: 700; }
			.old {
				@include fs(14rem);
				color: darken($white,40%);
				@include flexbox;
				.del {
					text-decoration: line-through;
					font-style: italic;
					font-weight: 600; }
				span {
					color: darken($white,60%);
					margin-left: 5px;
					@include fs(14rem);
					&:before {
						content: "(-";
						font-family: Montserrat; }
					&:after {
						content: "%)";
						font-family: Montserrat; } } }
			.price-des {
				@include fs(13rem);
				color: darken($white,40%);
				font-style: italic; } }
		.product-guarantee {
			@include fs(14rem);
			color: darken($white,60%);
			margin-bottom: 10px;
			padding-bottom: 10px;
			border-bottom: 1px dashed darken($white,10%);
			strong {
				color: $main; } }
		.product-option {
			margin-bottom: 10px;
			padding-bottom: 10px;
			border-bottom: 1px dashed darken($white,10%);
			.btn-showoption {
				@include fs(14rem);
				color: darken($white,60%);
				@include pos(rel,null);
				@include media-breakpoint-down(md) {
					@include bor-rad(5px);
					border: 1px solid lighten($blue,10%);
					background: color-rgba($blue,0.05);
					padding: 10px;
					&:before {
						content: '\f0ad';
						margin-right: 5px;
						display: inline;
						vertical-align: middle;
						@include pos(abs, 10px null null 10px); } }
				span {
					color: $main;
					font-weight: 500;
					margin-left: 5px; }
				.product-selected-options {
					@include media-breakpoint-down(md) {
						padding-left: 20px; } } }
			label {
				@include fs(14rem);
				font-weight: 500;
				color: $main;
				@include media-breakpoint-down(md) {
					margin-bottom: 5px; } }
			.option-list {
				ul {
					list-style-type: disc;
					padding-left: 20px;
					padding-top: 10px;
					li {
						@include fs(14rem);
						margin-bottom: 5px;
						a {
							font-weight: 600;
							margin-left: 5px;
							&:hover {
								color: $blue; } } } } }
			.option-group {
				// margin-top: 5px
				@include media-breakpoint-down(md) {
					display: none; } }
			.colors-list {
				display: block;
				white-space: nowrap;
				overflow: auto;
				@include disabledscrollbar;
				@include media-breakpoint-up(lg) {
					white-space: normal; }
				@include media-breakpoint-down(md) {
					margin: 0px -15px;
					padding: 0px 15px; }
				a {
					display: inline-block;
					vertical-align: top;
					width: 50px;
					margin-right: 5px;
					white-space: normal;
					@include media-breakpoint-up(lg) {
						margin-bottom: 5px; }
					@include media-breakpoint-down(md) {
						width: auto;
						margin-right: 10px; }
					.img {
						width: 50px;
						height: 50px;
						@include flexbox;
						align-items: center;
						align-content: center;
						justify-content: center;
						@include bor-rad(5px);
						border: 1px solid darken($white,10%);
						filter: grayscale(100%);
						@include media-breakpoint-down(md) {
							width: 150px;
							height: 150px; }
						img {
							max-width: 90%;
							max-height: 90%; } }
					.name {
						@include fs(13rem);
						@include textover(2);
						font-weight: 500;
						display: none;
						margin-top: 5px;
						@include media-breakpoint-down(md) {
							display: block; } }
					.price {
						@include fs(13rem);
						color: $red;
						font-weight: 700;
						display: none;
						@include media-breakpoint-down(md) {
							display: block; } }
					&:hover {
						.img {
							filter: grayscale(0); } }
					&.active {
						color: $blue;
						.img {
							filter: grayscale(0);
							border-color: $blue; } }
					&.disabled {
						color: darken($white,40%);
						opacity: 0.3; } } }
			.properties-list {
				display: block;
				white-space: nowrap;
				overflow: auto;
				@include disabledscrollbar;
				@include media-breakpoint-up(lg) {
					white-space: normal; }
				@include media-breakpoint-down(md) {
					margin: 0px -15px;
					padding: 0px 15px; }
				a {
					display: inline-block;
					vertical-align: top;
					@include fs(13rem);
					line-height: 1;
					padding: 3px 10px;
					font-weight: 500;
					color: darken($white,60%);
					@include bor-rad(5px);
					border: 1px solid darken($white,10%);
					margin-right: 5px;
					@include media-breakpoint-up(lg) {
						margin-bottom: 5px; }
					@include media-breakpoint-down(md) {
						@include fs(14rem);
						padding: 5px 10px;
						margin-right: 10px; }
					&:hover {
						color: $blue; }
					&.active {
						color: $blue;
						border-color: $blue;
						background: color-rgba($blue,0.05); }
					&.disabled {
						color: darken($white,40%);
						opacity: 0.3; } } } }
		.page-popup {
			.product-option {
				border-bottom: none; }
			.option-group {
				display: block; }
			.popup-body {
				.item {
					padding-bottom: 15px;
					// margin-bottom: 15px
					border-bottom: 1px dashed darken($white,10%);
					figure {
						@include flexbox;
						align-items: center;
						align-content: center;
						.product-img {
							width: 80px;
							height: 80px;
							@include flexbox;
							align-items: center;
							align-content: center;
							justify-content: center;
							border: 1px solid darken($white,10%);
							@include bor-rad(5px);
							overflow: hidden; }
						figcaption {
							padding-left: 15px;
							width: calc(100% - 80px);
							.product-name {
								@include fs(18rem);
								line-height: 1.3;
								font-weight: 500;
								color: $black;
								display: -webkit-box;
								-webkit-line-clamp: 2;
								-webkit-box-orient: vertical;
								overflow: hidden;
								text-overflow: ellipsis;
								padding-right: 0;
								margin-bottom: 5px; }
							.product-price {
								border: none;
								margin: 0;
								padding: 0;
								.new {
									@include fs(16rem);
									line-height: 1;
									font-weight: 700;
									color: $red; } } } } }
				.close-popup {
					margin-top: 20px;
					display: block;
					width: 100%;
					background: linear-gradient(to right, $amber, $deeporange);
					color: $white;
					@include bor-rad(50px);
					text-align: center;
					margin-bottom: 10px;
					text-transform: uppercase;
					@include fs(14rem);
					font-weight: 500;
					line-height: 40px;
					box-shadow: 0px 10px 20px -10px $deeporange; } } }

		.btn-like {
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center;
			width: 36px;
			height: 36px;
			@include bor-rad(50px);
			color: $white;
			background: $redgradient;
			@include pos(abs, 0 null null -50px);
			@include media-breakpoint-down(md) {
				width: auto;
				height: auto;
				background: none;
				@include bor-rad(0);
				color: darken($white,60%);
				left: unset;
				right: 0px;
				@include fs(24rem); }
			&:before {
				content: '\f08a'; }
			&.active {
				@include media-breakpoint-down(md) {
					color: lighten($red,10%); }
				&:before {
					content: '\f004'; } }
			&:hover {
				box-shadow: 0px 10px 20px -10px $red;
				@include media-breakpoint-down(md) {
					box-shadow: none; } } }
		.product-btn {
			@include flexbox;
			flex-wrap: wrap;
			margin-bottom: 10px;
			padding-bottom: 10px;
			border-bottom: 1px dashed darken($white,10%);
			@include media-breakpoint-down(md) {
				border: none;
				margin: 0;
				padding: 0 15px;
				background: $white;
				box-shadow: 0px 0px 20px color-rgba($black,0.05);
				@include pos(fix, null 0 0 0);
				z-index: 500;
				align-items: center;
				align-content: center;
				height: 50px; }
			.btn-buy {
				display: block;
				width: 100%;
				background: linear-gradient(to right, #59e1cb, #32b8e6);
				color: $white;
				@include bor-rad(50px);
				text-align: center;
				margin-bottom: 10px;
				text-transform: uppercase;
				@include fs(15rem);
				font-weight: 500;
				line-height: 40px;
				box-shadow: 0px 10px 20px -10px #59e1cb;
				@include media-breakpoint-down(md) {
					order: 2;
					flex: 1;
					// width: calc(70% - 10px)
					margin: 0;
					padding: 0;
					line-height: 36px;
					@include fs(14rem); }
				&:hover {
					filter: brightness(110%);
					box-shadow: 0px 10px 20px -10px #59e1cb; } }
			.btn-tragop {
				@include bor-rad(50px);
				border: 1px solid lighten($orange,10%);
				@include fs(14rem);
				line-height: 1.2;
				text-transform: uppercase;
				font-weight: 500;
				text-align: center;
				width: calc(50% - 5px);
				padding: 10px 0;
				@include media-breakpoint-down(lg) {
					@include fs(14rem); }
				@include media-breakpoint-down(md) {
					order: 1;
					background: $orangegradient;
					color: $white;
					border: none;
					line-height: 36px;
					padding: 0;
					width: 30%;
					@include fs(14rem);
					@include media-breakpoint-down(md) {
						margin-left: 0;
						margin-right: 10px; } }
				&.pc {
					@include media-breakpoint-down(md) {
						display: none; } }
				&.mobile {
					display: none;
					@include media-breakpoint-down(md) {
						display: block; } }
				.des {
					@include fs(12rem);
					text-transform: none;
					color: darken($white,40%); }
				+ .btn-tragop {
					margin-left: 10px;
					@include media-breakpoint-down(md) {
						margin-left: 0; } }
				&:hover {
					background: color-rgba($orange,0.1);
					color: $orange; } } }
		.product-box, .product-gift {
			@include bor-rad(5px);
			overflow: hidden;
			border: 1px solid darken($white,10%);
			margin-bottom: 10px;
			@include media-breakpoint-down(md) {
				margin: 10px 0; }
			.title {
				@include flexbox;
				align-items: center;
				align-content: center;
				background: color-rgba($blue,0.05);
				background: darken($white,3%);
				padding: 5px 10px;
				.img {
					width: 32px;
					height: 32px;
					@include flexbox;
					align-items: center;
					align-content: center;
					justify-content: center;
					img {
						max-width: 100%;
						max-height: 100%; } }
				span {
					@include fs(14rem);
					font-weight: 500;
					text-transform: uppercase;
					padding-left: 10px; } }
			.content {
				padding: 10px;
				@include fs(13rem);
 } }				// margin-bottom: -15px
		.product-gift {
			// border-color: lighten($blue,10%)
			.title {
				// background: color-rgba($blue,0.1)
				span {
 } }					// color: $blue
			a {
				&:hover {
					color: $blue; } }
			.gift-list {
				list-style-type: none;
				padding: 10px;
				max-height: 150px;
				overflow: hidden;
				@include pos(rel,null);
				@include transition(all,0.5s);
				&:after {
					content: '';
					@include pos(abs, null 0 0 0);
					z-index: 1;
					height: 30px;
					background: linear-gradient(color-rgba($white,0),color-rgba($white,1)); }
				&.active {
					max-height: 400px; }
				li {
					a {
						@include flexbox;
						align-items: center;
						align-content: center;
						.img {
							width: 40px;
							height: 40px;
							@include bor-rad(5px);
							@include flexbox;
							align-items: center;
							align-content: center;
							justify-content: center;
							img {
								max-width: 100%;
								max-height: 100%;
								margin: 0; } }
						.name {
							width: calc(100% - 40px);
							padding-left: 10px;
							@include fs(13rem);
							line-height: 1.2;
							font-weight: 500;
							@include textover(2); }
						&:hover {
							color: $blue; } } } }
			.btn-viewmoregift {
				@include fs(13rem);
				text-align: center;
				color: $blue;
				display: block;
				font-weight: 500;
				padding: 5px 0;
				&:after {
					content: '\f107';
					margin-left: 5px; } } }
		.product-support {
			@include fs(14rem);
			@include bor-rad(5px);
			overflow: hidden;
			border: 1px dashed darken($white,10%);
			padding: 10px;
			.title {
				@include fs(14rem);
				font-weight: 500;
				margin-bottom: 5px; }
			ul {
				@include fs(13rem);
				color: darken($white,60%);
				margin-bottom: 5px;
				li {
					padding-left: 15px;
					@include pos(rel,null);
					&:before {
						content: '\f058';
						@include pos(abs, 0 null null 0);
						color: $green; } } } } }
	.product-description {
		background: $white;
		padding: 15px;
		@include bor-rad(5px);
		box-shadow: 0px 5px 20px color-rgba($black,0.05);
		margin: 0 0 15px;
		@include pos(rel,null);
		@include media-breakpoint-down(md) {
			margin: 0px -15px 10px;
			box-shadow: none;
			@include bor-rad(0); }
		.specification-tab {
			display: none;
