.banner-wrap {
	@include pos(rel,null); }
.home-banner {
	@include pos(rel,null);
	margin: 30px 0 20px;
	background: $white;
	@include bor-rad(5px);
	overflow: hidden;
	box-shadow: 0px 5px 20px color-rgba($black,0.05);
	@include media-breakpoint-down(md) {
		margin: 15px 0px; }
	@include media-breakpoint-down(sm) {
		margin: 15px 0; } }
.banner-slide {
	.slick-prev, .slick-next {
		@include pos(abs, 50% null null null);
		@include transform(translateY(-50%));
		z-index: 1;
		@include fs(0);
		color: $white;
		text-shadow: 0px 1px 5px color-rgba($black,0.1);
		opacity: 0;
		@include transition(all,0.5s);
		&:before {
			content: '';
			font-family: 'linearicons-free';
			@include fs(28rem); } }
	.slick-prev {
		left: 0px;
		&:before {
			content: '\e875'; } }
	.slick-next {
		right: 0px;
		&:before {
			content: '\e876'; } }
	&:hover {
		.slick-prev, .slick-next {
			opacity: 1; }
		.slick-prev {
			left: 20px; }
		.slick-next {
			right: 20px; } }
	.item {
		.banner-img {
			display: block;
			img {
				width: 100%;
				height: 415px;
				object-fit: cover;
				@include media-breakpoint-only(lg) {
					height: 325px; }
				@include media-breakpoint-down(md) {
					height: auto; } } } } }
.banner-nav {
	padding: 15px 0;
	overflow: hidden;
	@include media-breakpoint-down(sm) {
		display: none; }
	.slick-list {
		margin-left: -1px; }
	.item {
		border-left: 1px solid darken($white,15%);
		.banner-des {
			@include fs(13rem);
			line-height: 1.2;
			text-align: center;
			padding: 0px 10px;
			display: -webkit-box;
			height: 0.8125rem*1.2*2;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;
			overflow: hidden;
			text-overflow: ellipsis;
			cursor: pointer; } }
	.slick-current {
		.item {
			.banner-des {
				font-weight: 700; } } } }
.banner-sale {
	margin-bottom: 10px;
	.banner {
		.slick-prev, .slick-next {
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			z-index: 1;
			@include fs(0);
			color: $white;
			text-shadow: 0px 1px 5px color-rgba($black,0.1);
			opacity: 0;
			@include transition(all,0.5s);
			&:before {
				content: '';
				font-family: 'linearicons-free';
				@include fs(28rem); } }
		.slick-prev {
			left: 0px;
			&:before {
				content: '\e875'; } }
		.slick-next {
			right: 0px;
			&:before {
				content: '\e876'; } }
		&:hover {
			.slick-prev, .slick-next {
				opacity: 1; }
			.slick-prev {
				left: 20px; }
			.slick-next {
				right: 20px; } }
		.slick-dots {
			@include pos(abs, null 0 10px 0);
			text-align: center;
			li {
				display: inline-block;
				width: 10px;
				height: 10px;
				@include bor-rad(50px);
				background: $white;
				+ li {
					margin-left: 8px; }
				&.slick-active {
					background: $bluegradient;
					box-shadow: 0px 10px 20px -10px $blue; }
				button {
					display: none; } } } }
	img {
		width: 100%; } }

// slick init
.slick-track {
	@include flexbox;
	margin: 0;
	.slick-slide {
		height: auto;
		> div {
			height: 100%; } } }
.slick-vertical {
	.slick-track {
		display: block;
		.slick-slide {
			height: auto;
			> div {
				height: auto; } } } }
.slick-slide {
	.item {
		display: block !important; } }
