// CART HEADER
.cart-header {
	background: $white;
	margin-bottom: 30px;
	box-shadow: 0px 5px 20px color-rgba($black,0.05);
	@include media-breakpoint-down(lg) {
		margin-bottom: 15px; }
	@include media-breakpoint-down(md) {
		box-shadow: none;
		@include pos(fix, 0 0 null 0);
		z-index: 1000; }
	.header-wrap {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: space-between; }
	.btn-back {
		@include fs(0);
		color: $main;
		&:before {
			content: 'arrow_back';
			@extend .material-icons;
			margin-right: 15px; } }
	.sitelogo {
		height: 60px;
		@include flexbox;
		align-items: center;
		align-content: center;
		@include media-breakpoint-down(lg) {
			height: 50px; }
		@include media-breakpoint-down(sm) {
			flex: auto; }
		a {
			display: block;
			img {
				height: 30px;
				@include media-breakpoint-down(lg) {
					height: 25px; } } } }
	.hotline {
		a {
			@include flexbox;
			align-items: center;
			align-content: center;
			color: $main;
			.material-icons {
				@include bor-rad(50px);
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				background: $bluegradient;
				color: $white;
				width: 34px;
				height: 34px;
				@include fs(18rem);
				@include media-breakpoint-down(sm) {
					width: auto;
					height: auto;
					@include fs(24rem);
					background: none;
					@include bor-rad(0);
					color: $main; } }
			span {
				padding-left: 10px;
				line-height: 1.1;
				@include fs(12rem);
				@include media-breakpoint-down(sm) {
					display: none; }
				strong {
					display: block;
					@include fs(16rem); } }
			&:hover {
				strong {
					color: $blue; } } } }
	.cartdes {
		float: right;
		clear: right;
		@include fs(14rem);
		color: darken($white,40%);
		@include media-breakpoint-down(xs) {
			display: none; }
		a {
			color: $white;
			&:hover {
				color: $blue; } } } }
