.cart-footer {
	background: $white;
	border-top: 1px solid darken($white,10%);
	margin-top: 30px;
	@include media-breakpoint-down(md) {
		margin-top: 0px; }
	.footer-wrap {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: space-between;
		padding: 15px 0;
		@include media-breakpoint-down(sm) {
			flex-wrap: wrap;
			justify-content: center; } }
	.contact-info {
		@include flexbox;
		align-items: center;
		align-content: center;
		@include media-breakpoint-down(sm) {
			width: 100%;
			flex-wrap: wrap;
			justify-content: center;
			margin-bottom: 10px; }
		a {
			@include flexbox;
			align-items: center;
			align-content: center;
			color: $main;
			margin-right: 20px;
			@include media-breakpoint-down(sm) {
				margin-right: 10px; }
			.material-icons {
				width: 40px;
				height: 40px;
				@include bor-rad(50px);
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				background: $bluegradient;
				color: $white;
				@include media-breakpoint-down(md) {
					width: 36px;
					height: 36px;
					@include bor-rad(50px);
					@include fs(18rem); }
				@include media-breakpoint-down(sm) {
					width: 30px;
					height: 30px;
					@include fs(14rem); } }
			span {
				@include fs(14rem);
				line-height: 1.2;
				padding-left: 10px;
				@include media-breakpoint-down(md) {
					@include fs(12rem); }
				@include media-breakpoint-down(sm) {
					@include fs(10rem); }
				strong {
					display: block;
					@include fs(18rem);
					@include media-breakpoint-down(md) {
						@include fs(14rem); }
					@include media-breakpoint-down(sm) {
						@include fs(12rem); } } }
			&:hover {
				strong {
					color: $blue; } } } }
	.payment {
		@include media-breakpoint-down(sm) {
			width: 100%; }
		.payment-icon {
			@include flexbox;
			flex-wrap: wrap;
			align-items: center;
			align-content: center;
			justify-content: center;
			.icon {
				margin-right: 10px;
				img {
					@include media-breakpoint-down(md) {
						width: 40px; } } } } }
	.copyright {
		border-top: 1px solid darken($white,10%);
		padding: 15px 0;
		@include fs(14rem);
		text-align: center;
		@include media-breakpoint-down(md) {
			@include fs(13rem); }
		@include media-breakpoint-down(sm) {
			@include fs(12rem); } } }
