.fullpage {
	padding-top: 110px;
	@include media-breakpoint-down(lg) {
		padding-top: 50px; } }
.homepage {
	header {
		@include media-breakpoint-down(lg) {
			.btn-showmenu {
				@include flexbox; }
			.btn-prevpage {
				display: none; }
			.mini-menu {
				display: none; } } } }
header {
	background: $white;
	@include pos(fix, 0 0 null 0);
	z-index: 1000;
	@include transition(all,0.5s);
	box-shadow: 0px 5px 20px color-rgba($black,0.05);
	@include media-breakpoint-down(lg) {
		box-shadow: none; }
	&.minimal {}
	.header-wrap {
		@include flexbox;
		flex-wrap: wrap;
		align-items: center;
		align-content: center; }
	.btn-showmenu {
		display: none;
		margin-right: 15px;
		@include media-breakpoint-down(lg) {
			align-items: center;
			align-content: center; } }
	.btn-prevpage {
		display: none;
		margin-right: 15px;
		@include media-breakpoint-down(lg) {
			@include flexbox;
			align-items: center;
			align-content: center; } }
	.overlay {
		@include pos(fix, 0 null 0 0);
		right: 0;
		opacity: 0.5;
		z-index: 500;
		background: $black;
		-webkit-backdrop-filter: blur(10px);
		backdrop-filter: blur(10px);
		display: none; }
	.sitelogo {
		@include flexbox;
		height: 70px;
		align-items: center;
		align-content: center;
		@include media-breakpoint-down(lg) {
			height: 50px;
			flex: auto; }
		a {
			display: block; }
		img {
			height: 30px;
			@include media-breakpoint-down(lg) {
				height: 25px; } } }
	// TOP MENU
	.top-menu {
		margin-left: 40px;
		@include media-breakpoint-down(lg) {
			display: none; }
		ul.menu-link {
			@include flexbox;
			> li {
				+ li {
					margin-left: 30px; }
				> a {
					display: block;
					@include fs(14rem);
					font-weight: 700;
					text-transform: uppercase; }
				&:hover {
					> a {
						color: $blue; } } } } }
	// HOTLINE
	.hotline {
		@include media-breakpoint-up(xl) {
			@include pos(fix, null null 20px 20px); }
		a {
			display: block;
			@include media-breakpoint-up(xl) {
				@include flexbox;
				width: 50px;
				height: 50px;
				align-items: center;
				align-content: center;
				justify-content: center;
				@include bor-rad(50px);
				color: $white;
				background: $bluegradient;
				&:after {
					content: '';
					display: block;
					@include pos(abs, 0 null 0 0);
					right: 0;
					background: $bluegradient;
					opacity: .5;
					@include bor-rad(50px);
					@include transform(scale3d(1.3,1.3,1.3));
					animation-name: hotline-animate;
					animation-duration: 1s;
					animation-iteration-count: infinite; } }
			.material-icons {
				display: block;
				@include media-breakpoint-up(xl) {
					@include pos(rel,null);
					z-index: 1;
					animation-name: phone-ring;
					animation-duration: 2s;
					animation-iteration-count: infinite; } } } }
	// SEARCH
	.search {
		margin-left: 40px;
		flex: auto;
		@include pos(rel,null);
		@include media-breakpoint-down(lg) {
			flex: unset;
			margin-left: 15px; }
		.search-toggle {
			display: none;
			@include media-breakpoint-down(lg) {
				display: block; }
			.material-icons {
				display: block; } }
		.search-wrap {
			@include media-breakpoint-down(lg) {
				display: none;
				@include pos(fix, 0 null 0 0);
				right: 0;
				z-index: 1000;
				background: $white; }
			.btn-closesearch {
				display: none;
				@include media-breakpoint-down(lg) {
					@include flexbox;
					align-items: center;
					align-content: center;
					@include pos(abs, 0 null null 15px);
					height: 50px;
					z-index: 5; } }
			.searchbox {
				@include pos(rel,null);
				z-index: 600;
				@include media-breakpoint-down(lg) {
					padding: 0px 15px;
					z-index: 1; }
				input {
					background: darken($white,3%);
					@include bor-rad(5px);
					@include fs(14rem);
					line-height: 20px;
					width: 100%;
					border: none;
					padding: 10px 15px;
					padding-right: 50px;
					@include media-breakpoint-down(lg) {
						margin: 7px 0;
						margin-left: 34px;
						padding: 8px 15px;
						width: calc(100% - 34px); }
					&::-webkit-input-placeholder {
						opacity: 0.5; } }
				.searchbutton {
					width: 40px;
					height: 40px;
					@include flexbox;
					align-items: center;
					align-content: center;
					justify-content: center;
					cursor: pointer;
					@include pos(abs, 0 0 null null);
					@include media-breakpoint-down(lg) {
						right: 15px;
						height: 36px;
						top: 7px; }
					&:hover {
						color: $blue; } }
				&.active {
					input {
						background: $white;
						box-shadow: 0px 10px 50px -10px color-rgba($black,0.1); } } } } }
	.col-right {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: flex-end; }
	.site-feature {
		@include flexbox;
		align-items: center;
		align-content: center;
		margin-left: 30px;
		.feature {
			@include flexbox;
			align-items: center;
			align-content: center;
			width: 33.33%;
			.icon {
				opacity: 0.6; }
			.name {
				@include fs(11rem);
				line-height: 1.3;
				color: darken($white,40%);
				width: 70px;
				padding-left: 10px; } } }
	// CHECK ORDER
	.check-tool {
		margin-left: 20px;
		@include media-breakpoint-down(lg) {
			margin-left: 0; }
		a {
			@include flexbox;
			align-items: center;
			align-content: center;
			@include fs(12rem);
			line-height: 1.1;
			font-weight: 500;
			width: 80px;
			@include media-breakpoint-down(lg) {
				width: 100%;
				padding: 10px 15px;
				font-weight: normal;
				@include fs(14rem);
				color: darken($white,60%); }
			&:before {
				@include media-breakpoint-down(lg); } }
		&.s1 {
			margin-left: 40px;
			@include media-breakpoint-down(lg) {
				margin-left: 0; }
			a {
				&:before {
					content: 'assignment';
					@extend .material-icons;
					@include fs(24rem);
					margin-right: 5px;
					@include media-breakpoint-down(lg) {
						margin-right: 10px; } } } }
		&.s2 {
			a {
				&:before {
					content: 'event_available';
					@extend .material-icons;
					@include fs(24rem);
					margin-right: 5px;
					@include media-breakpoint-down(lg) {
						margin-right: 10px; } } } }
		&:hover {
			a {
				color: $blue; } } }
	// LOGIN
	.login-tool {
		margin-left: 20px;
		@include media-breakpoint-down(lg) {
			display: none; }
		.dropdown-toggle {
			@include fs(14rem);
			font-weight: 700;
			text-transform: uppercase;
			@include flexbox;
			align-items: center;
			align-content: center;
			&:before {
				content: 'person';
				@extend .material-icons;
				@include fs(28rem);
				@include flexbox;
				align-items: center;
				align-content: center;
				height: 24px;
				margin-right: 5px; }
			&:after {
				display: none; }
			&:hover {
				color: $blue; } }
		.dropdown-menu {
			min-width: 270px;
			padding: 10px; } }
	.login {
		ul {
			li {
				margin-bottom: 10px;
				a {
					@extend .btn-main;
					display: block;
					text-transform: uppercase; } } } }
	// NOTIFICATION
	.notification {
		margin-left: 20px;
		@include media-breakpoint-down(lg) {
			display: none; }
		.dropdown-toggle {
			@include fs(0rem);
			font-weight: 700;
			text-transform: uppercase;
			@include flexbox;
			align-items: center;
			align-content: center;
			&:before {
				content: 'notifications';
				@extend .material-icons;
				@include fs(24rem); }
			&:after {
				display: none; }
			&:hover {
				color: $blue; } }
		.dropdown-menu {
			min-width: 350px;
			.notification-list {
				max-height: 300px;
				overflow: auto;
				@include scrollbar;
				ul {
					li {
						border-bottom: 1px dashed darken($white,5%);
						.notification-item {
							display: block;
							@include pos(rel,null);
							@include transition(all,0.5s);
							&:before {
								content: '';
								@include pos(abs, 0 null 0 0);
								width: 3px;
								background: $bluegradient-v;
								opacity: 0;
								@include transition(all,0.5s); }
							figure {
								@include flexbox;
								align-items: flex-start;
								align-content: flex-start;
								padding: 10px 15px;
								.img {
									width: 40px; }
								figcaption {
									width: calc(100% - 40px);
									padding-left: 10px;
									.name {
										@include fs(13rem);
										line-height: 1.3;
										@include textover(3);
										@include transition(all,0.5s); }
									time {
										@include fs(12rem);
										line-height: 1;
										color: darken($white,40%);
										display: inline-block;
										margin-top: 5px; } } }
							&:hover {
								background: color-rgba($blue,0.1);
								&:before {
									opacity: 1; }
								figure {
									figcaption {
										.name {
											color: $blue; } } } } } } } }
			.btn-viewallno {
				@include pos(static,null);
				font-weight: normal;
				padding: 7px 0;
				text-align: center;
				display: block;
				border-top: 1px solid darken($white,10%);
				color: $blue;
				@include fs(14rem);
				&:after {
					content: '\f105';
					margin-left: 5px; }
				&:hover {
					color: $main; } } } }
	// CART
	.cart {
		margin-left: 20px;
		@include pos(rel,null);
		@include media-breakpoint-down(lg) {
			margin-left: 15px; }
		.cart-toggle {
			.cart-amount {
				@include fs(0rem);
				font-weight: 700;
				text-transform: uppercase;
				@include flexbox;
				align-items: center;
				align-content: center;
				@include pos(rel,null);
				&:before {
					content: 'shopping_cart';
					@extend .material-icons;
					@include fs(24rem);
					@include transition(all,0.5s); }
				&:hover {
					color: $blue; }
				.amount {
					@include fs(10rem);
					line-height: 1;
					@include bor-rad(50px);
					padding: 2px 5px;
					color: $white;
					background: $orangegradient;
					@include pos(abs, -3px -5px null null); } } }
		.cart-panel {
			@extend .dropdown-menu;
			@extend .dropdown-menu-right;
			min-width: 350px;
			.dropdown-title {
				@include pos(rel,null);
				@include media-breakpoint-down(sm) {
					@include flexbox;
					align-items: center;
					align-content: center;
					padding: 0 15px; } }
			.cart-list {
				max-height: 300px;
				overflow: auto;
				@include scrollbar;
				@include media-breakpoint-down(sm) {
					max-height: unset;
					height: calc(100vh - 102px); }
				.item {
					border-bottom: 1px dashed darken($white,5%);
					&:last-child {
						border-bottom: none; } }
				.cart-item {
					display: block;
					padding: 15px 15px;
					@include pos(rel,null);
					@include transition(all,0.5s);
					&:before {
						content: '';
						@include pos(abs, 0 null 0 0);
						width: 3px;
						background: $bluegradient-v;
						opacity: 0;
						@include transition(all,0.5s); }
					figure {
						@include flexbox;
						align-items: flex-start;
						align-content: flex-start;
						.img {
							width: 60px;
							height: 60px;
							@include flexbox;
							align-items: center;
							align-content: center;
							justify-content: center;
							img {
								max-width: 90%;
								max-height: 90%; } }
						figcaption {
							width: calc(100% - 60px);
							padding-left: 10px;
							.name {
								@include fs(13rem);
								line-height: 1.3;
								font-weight: 500;
								display: -webkit-box;
								-webkit-line-clamp: 2;
								-webkit-box-orient: vertical;
								overflow: hidden;
								text-overflow: ellipsis;
								@include transition(all,0.5s); }
							.amount {
								@include fs(13rem);
								@include flexbox;
								align-items: center;
								align-content: center;
								.input-number {
									margin-left: 5px;
									// width: 60px
									// +fs(13rem)
									// +bor-rad(5px)
									// overflow: hidden
									// text-align: center
 } }									// border: 1px solid darken($white,10%)
							.price {
								margin-top: 5px;
								.new {
									color: $bluegrey;
									@include fs(13rem);
									line-height: 1;
									font-weight: 700; } } } }
					&:hover {
						// background: color-rgba($blue,0.1)
						&:before {
							opacity: 1; }
						figure {
							figcaption {
								.name {
									color: $blue; } } } } } }
			.cart-checkout {
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: space-between;
				padding: 9px 15px;
				border-top: 1px solid darken($white,10%);
				@include media-breakpoint-down(sm) {
					@include pos(abs, null 0 0 0);
					z-index: 5; }
				.cart-total {
					@include fs(13rem);
					strong {
						color: $red;
						@include fs(15rem);
						margin-left: 5px; } }
				.btn-checkout {
					@extend .btn-main;
					line-height: 32px;
					padding: 0px 20px; } } } }
	.mini-menu {
		display: none;
		margin-left: 15px;
		@include media-breakpoint-down(lg) {
			display: block; }
		.dropdown-toggle {
			.material-icons {
				display: block; } }
		.dropdown-menu {
			min-width: 200px;
			padding: 5px 0;
			.dropdown-item {
				@include flexbox;
				align-items: center;
				align-content: center;
				padding: 5px 10px;
				.material-icons {
					width: 30px;
					height: 30px;
					@include flexbox;
					align-items: center;
					align-content: center;
					justify-content: center; }
				span {
					width: calc(100% - 30px);
					padding-left: 10px;
					@include fs(13rem); } } } }
	.header-menu {} }
