.product-subcate {
	@include media-breakpoint-down(md) {
		display: none; }
	.category-list {
		display: block;
		@include media-breakpoint-down(lg) {
			white-space: nowrap;
			overflow: auto;
			overflow-y: hidden; }
		.item {
			display: inline-block;
			vertical-align: top;
			.category-item {
				display: block;
				text-align: center;
				padding: 10px;
				width: 100px;
				height: 100%;
				white-space: normal;
				@include bor-rad(5px);
				.img {
					@include flexbox;
					align-items: center;
					align-content: center;
					justify-content: center;
					width: 80px;
					height: 80px;
					background: $white;
					box-shadow: 0px 10px 20px -10px color-rgba($black,0.3);
					@include bor-rad(50px);
					margin-bottom: 5px;
					@include fs(0);
					img {
						max-width: 32px;
						max-height: 32px;
						filter: brightness(0%);
						opacity: 0.5; } }
				.name {
					@include fs(13rem);
					line-height: 1.3;
					font-weight: 500; } }
			&:hover {
				.category-item {
					.img {
						background: $bluegradient; } } }
			&.active {
				.category-item {
					.img {
						background: $bluegradient;
						img {
							filter: brightness(0%)invert(100%);
							opacity: 1; } }
					.name {
						color: $blue; } } } } } }
.product-page {
	background: $white;
	padding: 15px;
	@include bor-rad(5px);
	margin-bottom: 15px;
	box-shadow: 0px 5px 20px color-rgba($black,0.05);
	@include media-breakpoint-down(md) {
		margin: 0 -15px 10px;
		@include bor-rad(0);
		box-shadow: none; }
	.product-list {
		margin-bottom: 15px;
		@include media-breakpoint-down(md) {
			margin: 0px 0px 15px; }
		article {
			&.col-6 {
				&.change {
					flex: 0 0 100%;
					max-width: 100%;
					.product-col {
						figure {
							@include flexbox;
							@include pos(rel,null);
							.product-img {
								width: 120px;
								height: 120px;
								margin: 0; }
							figcaption {
								width: calc(100% - 120px);
								padding-left: 15px; } } } } }
			&.col-12 {
				&.col-lg-2 {
					@include media-breakpoint-down(sm) {
						// flex: 0 0 100%
						// max-width: 100%
						.product-col {
							figure {
								@include flexbox;
								@include pos(rel,null);
								.product-img {
									width: 120px;
									height: 120px;
									margin: 0; }
								figcaption {
									width: calc(100% - 120px);
									padding-left: 15px; } } } } } } } }
	&.compare-active {
		.product-item {
			.btn_compare {
				visibility: visible;
				opacity: 1; } } }
	.btn-loadmore {
		@include fs(14rem);
		font-weight: 500;
		color: $white;
		background: $bluegradient;
		@include bor-rad(50px);
		line-height: 36px;
		display: inline-block;
		padding: 0px 30px;
		@include media-breakpoint-down(sm) {
			margin: 15px 0;
			@include fs(13rem);
			line-height: 36px; }
		&:before {
			content: '\f055';
			font-family: 'fontawesome';
			margin-right: 10px; }
		&:hover {
			box-shadow: 0px 10px 20px -10px $blue; } } }
.product-page-intro {
	background: $white;
	@include bor-rad(5px);
	overflow: hidden;
	margin-bottom: 15px;
	box-shadow: 0px 5px 20px color-rgba($black,0.05);
	@include flexbox;
	flex-wrap: wrap;
	@include media-breakpoint-down(md) {
		margin: 10px -15px;
		@include bor-rad(0);
		box-shadow: none; }
	.bg {
		width: 35%;
		@include media-breakpoint-down(lg) {
			width: 40%; }
		@include media-breakpoint-down(sm) {
			width: 100%; }
		img {
			width: 100%;
			@include media-breakpoint-down(lg) {
				height: 100%;
				object-fit: cover; } } }
	.caption {
		width: 65%;
		padding: 20px;
		@include media-breakpoint-down(lg) {
			width: 60%; }
		@include media-breakpoint-down(sm) {
			width: 100%; }
		.name {
			@include fs(24rem);
			font-weight: 700;
			margin-bottom: 10px; }
		.content {
			@include fs(14rem); } } }
