.hot-news {
	margin: 15px 0;
	padding: 15px 15px 0;
	background: $white;
	@include bor-rad(5px);
	overflow: hidden;
	box-shadow: 0px 5px 20px color-rgba($black,0.05);
	@include media-breakpoint-down(md) {
		@include bor-rad(0);
		box-shadow: none;
		margin: 0px -15px 10px; }
	.news-list {
		.news-item {
			display: block;
			@include pos(rel,null);
			@include bor-rad(5px);
			overflow: hidden;
			box-shadow: 0px 5px 20px color-rgba($black,0.05);
			margin-bottom: 20px;
			@include media-breakpoint-down(sm) {
				display: none; }
			.img {
				img {
					width: 100%;
					height: 250px;
					object-fit: cover;
					@include bor-rad(5px);
					filter: brightness(90%);
					@include transition(all,0.5s);
					@include media-breakpoint-down(lg) {
						height: 220px; } } }
			.caption {
				@include pos(abs, null 0 0 0);
				padding: 15px;
				background: linear-gradient(color-rgba($black,0),color-rgba($black,0.5));
				color: $white;
				text-shadow: 0px 1px 5px color-rgba($black,0.1);
				.category {
					@include fs(12rem);
					font-weight: 700;
					text-transform: uppercase;
					margin-bottom: 10px; }
				.name {
					@include fs(18rem);
					@include textover(2);
					line-height: 1.2;
					font-weight: 700;
					margin-bottom: 10px; }
				time {
					@include fs(12rem); } }
			&.big {
				@include media-breakpoint-down(sm) {
					display: block; }
				.img {
					img {
						height: 520px;
						@include media-breakpoint-down(lg) {
							height: 460px; }
						@include media-breakpoint-down(sm) {
							height: 300px; } } }
				.caption {
					.name {
						@include fs(24rem);
						@include media-breakpoint-down(sm) {
							@include fs(18rem); } } } }
			&:hover {
				.img {
					img {
						filter: brightness(110%); } } } } } }
.news-category {
	margin: 15px 0;
	padding: 15px;
	background: $white;
	@include bor-rad(5px);
	overflow: hidden;
	box-shadow: 0px 5px 20px color-rgba($black,0.05);
	@include media-breakpoint-down(md) {
		@include bor-rad(0);
		box-shadow: none;
		margin: 0px -15px 10px; }
	.category-wrap {}
	.category-list {
		@include flexbox;
		flex-wrap: wrap;
		margin: 0px -5px;
		@include media-breakpoint-down(md) {
			display: block;
			margin: 0px -15px;
			white-space: nowrap;
			overflow: auto;
			padding: 0px 15px; }
		.item {
			flex: 1;
			padding: 0px 5px;
			@include media-breakpoint-down(md) {
				display: inline-block;
				vertical-align: top;
				width: 150px;
				padding: 0;
				+ .item {
					margin-left: 10px; } }
			.category-item {
				@include pos(rel,null);
				height: 100px;
				@include bor-rad(5px);
				overflow: hidden;
				box-shadow: 0px 5px 20px color-rgba($black,0.05);
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				@include media-breakpoint-down(lg) {
					height: 80px; }
				@include media-breakpoint-down(sm) {
					height: 60px; }
				&:hover {
					@include media-breakpoint-up(md) {
						margin-top: -5px;
						box-shadow: 0px 5px 20px color-rgba($black,0.3); } }
				.img {
					@include pos(abs, 0 null 0 0);
					right: 0;
					&:before {
						content: '';
						@include pos(abs, 0 null 0 0);
						right: 0;
						z-index: 1;
						background: linear-gradient(color-rgba($blue,0.1),color-rgba($blue,0.7));
						opacity: 0; }
					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
						filter: brightness(50%); } }
				.name {
					@include fs(16rem);
					line-height: 1.2;
					color: $white;
					font-weight: 500;
					text-transform: uppercase;
					@include pos(rel,null);
					z-index: 3;
					text-shadow: 0px 1px 5px color-rgba($black,0.1);
					padding: 0px 15px;
					text-align: center;
					white-space: normal;
					@include media-breakpoint-down(lg) {
						@include fs(14rem); }
					@include media-breakpoint-down(sm) {
						padding: 0px 20px; } } }
			&.active {
				.category-item {
					box-shadow: 0px 10px 20px -10px $blue;
					.img {
						&:before {
							opacity: 1; } } } } } } }
