 } } } } }						// display: none
	.news-slide {
		@include bor-rad(5px);
		overflow: hidden;
		box-shadow: 0px 5px 20px color-rgba($black,0.05);
		margin-bottom: 10px;
		.slick-prev, .slick-next {
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			z-index: 1;
			@include fs(0);
			color: $white;
			text-shadow: 0px 1px 5px color-rgba($black,0.1);
			opacity: 0;
			@include transition(all,0.5s);
			&:before {
				content: '';
				font-family: 'linearicons-free';
				@include fs(28rem); } }
		.slick-prev {
			left: 0px;
			&:before {
				content: '\e875'; } }
		.slick-next {
			right: 0px;
			&:before {
				content: '\e876'; } }
		&:hover {
			.slick-prev, .slick-next {
				opacity: 1; }
			.slick-prev {
				left: 20px; }
			.slick-next {
				right: 20px; } }
		.slick-dots {
			@include pos(abs, null 0 10px 0);
			text-align: center;
			li {
				display: inline-block;
				width: 10px;
				height: 10px;
				@include bor-rad(50px);
				background: darken($white,10%);
				&.slick-active {
					background: $bluegradient; }
				button {
					display: none; }
				+ li {
					margin-left: 5px; } } }
		.news-item {
			display: block;
			@include pos(rel,null);
			.img {
				img {
					width: 100%;
					height: 400px;
					object-fit: cover;
					@include media-breakpoint-down(lg) {
						height: 350px; }
					@include media-breakpoint-down(sm) {
						height: 250px; } } }
			.caption {
				@include pos(abs, null 0 0px 0);
				padding: 30px 15px;
				background: linear-gradient(color-rgba($black,0),color-rgba($black,0.5));
				color: $white;
				text-shadow: 0px 1px 5px color-rgba($black,0.1);
