.sidebar-video {
	padding: 15px;
	margin-bottom: 15px;
	background: $white;
	@include bor-rad(5px);
	overflow: hidden;
	box-shadow: 0px 5px 20px color-rgba($black,0.05);
	@include media-breakpoint-down(md) {
		@include bor-rad(0);
		box-shadow: none;
		margin: 0px -15px 10px; }
	.video-wrap {
		margin: 0px -15px -15px;
		@include media-breakpoint-down(sm) {
			box-shadow: none; }
		.video-des {
			color: $main;
			.video-name {
				@include fs(13rem);
				font-weight: 500;
				@include textover(2); }
			.video-view {
				@include fs(12rem);
				opacity: 0.8;
				font-weight: 300; } }
		.video-big {
			.video-des {
				padding: 15px;
				background: $mainblue; } }
		.video-list {
			background: $white;
			border-top: 1px dashed darken($white,10%);
			@include media-breakpoint-down(md) {
				@include flexbox;
				flex-wrap: wrap; }
			.video {
				display: block;
				padding: 10px;
				@include transition(all,0.5s);
				@include pos(rel,null);
				border-bottom: 1px dashed darken($white,10%);
				@include media-breakpoint-down(md) {
					width: 50%;
					border-right: 1px dashed darken($white,10%);
					&:nth-child(2n) {
						border-right: none; } }
				@include media-breakpoint-down(sm) {
					width: 100%;
					border-right: none;
					&:first-child {
						border-top: none; }
					&:last-child {
						border-bottom: none; } }
				&:before {
					content: '';
					@include pos(abs, 0 null 0 0);
					width: 3px;
					background: $bluegradient-v;
					opacity: 0;
					@include transition(all,0.5s); }
				figure {
					@include flexbox;
					align-items: flex-start;
					align-content: flex-start;
					.video-img {
						width: 90px;
						@include pos(rel,null);
						&:before {
							content: '\f04b';
							background: $bluegradient-r;
							@include fs(12rem);
							width: 35px;
							height: 25px;
							@include bor-rad(5px);
							@include flexbox;
							align-items: center;
							align-content: center;
							justify-content: center;
							color: $white;
							@include pos(abs,50% null null 50%);
							@include transform(translate(-50%,-50%)); } }
					figcaption {
						width: calc(100% - 90px);
						padding-left: 10px; } }
				&:hover {
					background: color-rgba($blue,0.1);
					&:before {
						opacity: 1; } } } } } }
