					@include fs(14rem); }
				a {
					color: $black; } }
			.price {
				margin-top: 5px;
				@include pos(rel,null);
				.promotion {
					@include pos(abs, 2px 0 null null);
					@include fs(11rem);
					font-weight: 600;
					color: $white;
					padding: 0px 5px;
					@include bor-rad(3px);
					&.off {
						background: linear-gradient(to right, $orange, $deeporange); } }
				.new {
					@include fs(14rem);
					line-height: 1;
					font-weight: 700;
					color: $red;
					@include media-breakpoint-down(lg) {
 } }						// +fs(12rem)
				.old {
					@include fs(10rem);
					line-height: 1;
					color: darken($white,40%);
					text-decoration: line-through; } }
			.rating {
				@include flexbox;
				align-items: center;
				align-content: center;
				padding: 5px 0;
				flex-wrap: wrap;
				@include media-breakpoint-down(lg);
				.br-wrapper {
					margin-right: 5px; }
				span {
					@include fs(12rem);
					color: darken($white,40%); } }
			.des {
				@include fs(12rem);
				line-height: 1.4;
				white-space: normal;
				margin-top: 5px;
				color: $main;
				ul {
					list-style-type: disc;
					padding-left: 20px;
					li {} } }
			.btn-wrap {
				@include media-breakpoint-down(sm) {
					padding-top: 10px;
					@include flexbox;
					align-items: center;
					align-content: center;
					justify-content: space-between; }
				.btn-buy, .btn-tragop {
					display: inline-block;
					width: 150px;
					padding: 0px 20px;
					@include bor-rad(50px);
					@include fs(14rem);
					font-weight: 500;
					text-align: center;
					line-height: 36px;
					color: $white;
					@include media-breakpoint-down(sm) {
						@include fs(13rem);
						padding: 0px 15px;
						line-height: 34px;
						width: 49%; } }
				.btn-buy {
					background: $bluegradient;
					@include pos(abs, 20px 20px null null);
					@include media-breakpoint-down(sm) {
						@include pos(static,null); }
					&:hover {
						box-shadow: 0px 10px 20px -10px $blue; } }
				.btn-tragop {
					background: $orangegradient;
					@include pos(abs, 60px 20px null null);
					@include media-breakpoint-down(sm) {
						@include pos(static,null); }
					&:hover {
						box-shadow: 0px 10px 20px -10px $orange; } } } } }
	.share {
		border-top: 1px solid darken($white,10%);
		margin-top: 15px;
		padding-top: 15px; } }

.life-style {
	margin-top: 30px;
	box-shadow: 0px 5px 20px color-rgba($black,0.05);
	.item {
		.pic {
			display: block;
			img {
				width: 100%;
				height: 200px;
				object-fit: cover;
				@include media-breakpoint-down(lg) {
					height: 150px; }
				@include media-breakpoint-down(md) {
					height: 130px; }
				@include media-breakpoint-down(sm) {
					height: 100px; } } } } }
