				@include bor-rad(5px);
				height: calc(100% - 0px);
				background: darken($white,5%);
				// box-shadow: 0px 5px 20px color-rgba($black,0.05)
				// margin-bottom: 20px
				@include media-breakpoint-down(md) {
					margin-bottom: 0; }
				figure {
					.fb-img {
						@include flexbox;
						align-items: center;
						align-content: center;
						justify-content: center;
						height: 150px;
						overflow: hidden;
						@include bor-rad(5px 5px 0 0);
						@include pos(rel,null);
						@include media-breakpoint-down(lg) {
							height: 100px; }
						@include media-breakpoint-down(sm) {
							height: 150px; }
						&:before {
							content: '\f09a';
							background: color-rgba($facebook,0.8);
							@include fs(24rem);
							width: 50px;
							height: 40px;
							@include flexbox;
							align-items: center;
							align-content: center;
							justify-content: center;
							color: $white;
							@include pos(abs, null null 0 0); }
						img {
							min-width: 100%;
							min-height: 100%;
							@include media-breakpoint-down(md) {
								object-fit: cover; } } }
					figcaption {
						padding: 10px;
						time {
							display: inline-block;
							@include fs(13rem);
							color: darken($white,40%);
							margin-bottom: 5px;
							@include media-breakpoint-down(lg) {
								@include fs(12rem); } }
						.fb-des {
							@include fs(14rem);
							line-height: 1.4;
							color: $main;
							display: -webkit-box;
							-webkit-line-clamp: 3;
							-webkit-box-orient: vertical;
							overflow: hidden;
							text-overflow: ellipsis;
							@include transition(all,0.5s);
							@include media-breakpoint-down(lg) {
 }								// +fs(13rem)
							@include media-breakpoint-down(md) {
								white-space: normal;
								height: 0.875rem*3*1.4; } } } }
				&:hover {
					figure {
						figcaption {
							.fb-des {
								color: $blue; } } } } } } } }
