.notfound-page {
	padding: 60px 0;
	background: $white;
	@include media-breakpoint-down(sm) {
		padding: 40px 0; }
	.img {
		display: block;
		text-align: center;
		margin-bottom: 10px;
		img {
			max-width: 80%;
			@include media-breakpoint-down(lg) {
				max-width: 100%; }
			@include media-breakpoint-down(sm) {
				max-width: 80%; } } }
	.error-wrap {
		@include media-breakpoint-down(sm) {
			text-align: center; } }
	.error-title {
		@include fs(24rem);
		font-weight: 600;
		letter-spacing: 5px;
		color: $white;
		text-transform: uppercase;
		display: inline-block;
		padding: 10px 50px;
		@include bor-rad(10px);
		background: $bluegradient;
		box-shadow: 0px 10px 20px -10px $blue;
		margin-bottom: 20px;
		@include media-breakpoint-down(lg) {
			@include fs(22rem); }
		@include media-breakpoint-down(sm) {
			@include fs(20rem);
			padding: 10px 30px;
			text-align: center; } }
	.error-content {
		display: block;
		.title {
			@include fs(28rem);
			font-weight: 600;
			color: $blue;
			margin-bottom: 15px; }
		.content {
			color: darken($white,60%);
			@include fs(16rem);
			@include media-breakpoint-down(lg) {
				@include fs(14rem); } } }
	.product-subcate {
		text-align: center;
		margin-bottom: 20px;
		@include media-breakpoint-down(md) {
			display: block; }
		.category-list {
			@include media-breakpoint-down(sm) {
				@include flexbox;
				flex-wrap: wrap; }
			.item {
				@include media-breakpoint-down(sm) {
					width: 33.33%; }
				.category-item {
					@include media-breakpoint-down(sm) {
						width: 100%;
						padding: 0;
						margin-bottom: 10px;
						text-align: center;
						.img {
							margin: 0 auto 10px; } } } } } }
	.btn-wrap {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: center;
		flex-wrap: wrap;
		a {
			@include fs(14rem);
			font-weight: 500;
			color: $blue;
			display: inline-block;
			padding: 10px 20px;
			@include bor-rad(5px);
			@include media-breakpoint-down(sm) {
				display: block; }
			+ a {
				margin-left: 50px;
				@include media-breakpoint-down(sm) {
					margin-left: 0; } }
			&:hover {
				color: $orange;
				background: color-rgba($orange,0.1); } } } }
