.breadcrumb-wrap {
	padding: 10px 0;
	@include media-breakpoint-down(lg) {
		padding: 8px 0; }
	@include media-breakpoint-down(md) {
		background: $white; }
	.breadcrumb {
		margin: 0;
		padding: 0;
		@include bor-rad(0);
		background: none;
		@include media-breakpoint-down(md) {
			white-space: nowrap;
			overflow: auto;
			display: block;
			// display: -webkit-box
			// -webkit-line-clamp: 1
			// -webkit-box-orient: vertical
			// overflow: hidden
 }			// text-overflow: ellipsis
		li {
			display: inline-block;
			@include pos(rel,null);
			margin-right: 30px;
			&:after {
				content: '';
				display: block;
				width: 0;
				padding: 0;
				border: 4px solid transparent;
				border-color: transparent $blue $blue transparent;
				@include pos(abs, null -15px 6px null); }
			&:nth-child(2) {
				&:after {
					border-color: transparent $red $red transparent; } }
			&:nth-child(3) {
				&:after {
					border-color: transparent $purple $purple transparent; } }
			&:nth-child(4) {
				&:after {
					border-color: transparent $green $green transparent; } }
			&:nth-child(5) {
				&:after {
					border-color: transparent $amber $amber transparent; } }
			&:nth-child(6) {
				&:after {
					border-color: transparent $orange $orange transparent; } }
			a {
				color: darken($white,40%);
				@include fs(13rem);
				@include media-breakpoint-down(lg) {
					@include fs(12rem); }
				&:hover {
					color: $black; } } } } }
